//
// Checkbox, Radio and Switch inputs
//
@import '../settings/variables/color';
@import '../settings/mixins/icons';
@import '../settings/mixins/visibility';
@import '../typography/variables/font-weights';

.ef-boolean__element {
  position: relative;
  width: 24px;
  height: 24px;
  display: flex;
  flex: 0 0 auto;
  align-self: flex-start;
  align-items: center;
  justify-content: center;
  background-color: var(--input-bg);
  border: 1px solid var(--input-border);
  transition: background-color 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: pointer;
  overflow: hidden;

  &:before {
    content: '';
    width: 12px;
    height: 12px;
    z-index: 1;
    display: block;
  }

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: -20%;
    width: 140%;
    height: 100%;
    z-index: 0;
    display: block;
    border-radius: 0;
    opacity: 0;
    transition: opacity 0.2s ease;
    box-shadow: inset 0 12px 24px rgba(white, 0.2);
  }

  &.-checkbox {
    border-radius: 4px;

    &:before {
      transform: scale(0);
      visibility: hidden;
      transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
      @include iconBG(check);
    }
  }

  &.-radio {
    border-radius: 50%;

    &:before {
      transform: scale(0);
      visibility: hidden;
      border-radius: 50%;
      transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
      background-color: var(--color-white, $color-white);
    }
  }

  &.-switch {
    width: 56px;
    padding: 4px;
    border-radius: 12px;
    justify-content: flex-start;

    &:before {
      width: 16px;
      height: 16px;
      transform: translateX(0);
      transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.05);
      border-radius: 50%;
      background-color: var(--color-text--disabled, $color-text--disabled);
    }
  }
}

.ef-boolean__input {
  // We hide the input visually, but use its state to
  // to manage the styling of the custom checkbox, radio
  // and switch elements.
  @include hideVisual();

  &:checked {
    // only apply checked styles to enabled inputs
    &:not(:disabled):not([disabled]) {
      ~ .-checkbox,
      ~ .-radio,
      ~ .-switch {
        overflow: hidden;
        border: none;
        position: relative;
        background-color: var(--color-ui-accent, $color-ui-accent);

        &:after {
          opacity: 1;
        }
      }
    }

    ~ .-checkbox,
    ~ .-radio,
    ~ .-switch {
      box-shadow: none;
    }

    ~ .-checkbox:before,
    ~ .-radio:before {
      transform: scale(1);
      visibility: visible;
    }

    ~ .-checkbox:before {
      @include iconBG(check--active);
    }

    ~ .-switch:before {
      transform: translateX(32px);
      background-color: var(--color-white, $color-white);
    }
  }

  &:disabled,
  &[disabled] {
    ~ .ef-boolean__element {
      pointer-events: none;
    }

    ~ .ef-boolean__label {
      color: var(--color-text--inactive);
      pointer-events: none;
    }

    ~ .-checkbox,
    ~ .-radio,
    ~ .-switch {
      background-color: var(--input-bg--disabled);
      border-color: var(--input-border--disabled);
      box-shadow: none;
    }

    ~ .-switch:before {
      opacity: 0.3;
    }

    &:checked {
      ~ .-radio:before {
        background-color: rgba(var(--color-text, $color-text), 0.15);
      }

      ~ .-checkbox:before {
        @include iconBG(check--disabled);
      }

      ~ .-switch:before {
        transform: translateX(30px);
        background-color: var(--color-text--disabled, $color-text--disabled);
      }
    }
  }

  &:focus,
  &.-focus {
    ~ .-checkbox,
    ~ .-radio,
    ~ .-switch {
      border-color: var(--color-ui-accent, $color-ui-accent);
      box-shadow: var(--focus--box-shadow);
      transition: box-shadow 0.15s ease;
    }
  }
}

.ef-boolean__label {
  padding: 0 pad(xs);
  color: var(--color-text, $color-text);
  font-weight: var(--fw-book, $fw-book);
  font-size: 14px;
  user-select: none;
  line-height: 20px;
  cursor: pointer;
}

// Boolean validation styles
.ef-boolean.-is-invalid {
  .ef-boolean__element {
    border-color: var(
      --color-notification-warning,
      $color-notification-warning
    );
  }

  .ef-boolean__input {
    &:focus {
      ~ .-radio,
      ~ .-checkbox,
      ~ .-switch {
        box-shadow: var(--focus--box-shadow);
      }
    }

    &:checked {
      ~ .-radio,
      ~ .-checkbox,
      ~ .-switch {
        background-color: var(
          --color-notification-warning,
          $color-notification-warning
        );
      }

      ~ .-radio:before,
      ~ .-switch:before {
        background-color: var(--color-white, $color-white);
      }
    }
  }
}
