@import '../settings/variables/color';

.ef-progress {
  &.-bar {
    display: flex;
    flex-wrap: wrap;
  }

  .ef-icon,
  .ef-progress__label {
    opacity: 0.5;
  }
}

.ef-progress__bar {
  position: relative;
  flex: 1 1 100%;
  overflow: hidden;

  &.-s {
    height: 4px;
    border-radius: 2px;
  }

  &.-m {
    height: 8px;
    border-radius: 4px;
  }

  &.-l {
    height: 16px;
    border-radius: 8px;
  }

  &.-indeterminate {
    .ef-progress__bar__indicator {
      left: -45.166611%;
      transform: scaleX(0);
      transition: none;
      animation: indeterminate-translate 2s linear infinite;

      .ef-progress__bar__indicator__inner {
        transition: none;
        animation: indeterminate-scale 2s linear infinite;
      }
    }
  }
}

.ef-progress__bar__track,
.ef-progress__bar__indicator,
.ef-progress__bar__indicator__inner {
  position: absolute;
  width: 100%;
  height: 100%;
}

.ef-progress__bar__track {
  background-color: rgba(var(--color-ink-black, $color-ink-black), 0.2);
}

.ef-progress__bar__indicator {
  transform-origin: top left;
  transition: transform 0.25s cubic-bezier(0.4, 0, 0.6, 1);
}

.ef-progress__bar__indicator,
.ef-progress__bar__indicator__inner {
  animation: none;
  transition: transform 0.25s cubic-bezier(0.4, 0, 0.6, 1);
}

.ef-progress__bar__indicator__inner {
  display: inline-block;
  background-color: var(--color-ui-accent, $color-ui-accent);
}

.ef-progress.-label-top .ef-progress__label {
  width: 100%;
  text-align: right;
}

.ef-progress.-label-inline {
  height: 16px;
  align-items: center;

  > * + * {
    margin-left: 8px;
  }

  .ef-progress__bar {
    flex-basis: auto;
  }
}

.ef-progress__label {
  font-size: 12px;

  .ef-progress.-label-top & {
    margin: 0 0 8px auto;
  }
}

@keyframes indeterminate-translate {
  0% {
    transform: translateX(0);
    animation-timing-function: cubic-bezier(0.15, 0, 0.515058, 0.409685);
  }
  25% {
    transform: translateX(37.651913%);
    animation-timing-function: cubic-bezier(0.31033, 0.284058, 0.8, 0.733712);
  }
  48.35% {
    transform: translateX(84.386165%);
    animation-timing-function: cubic-bezier(0.4, 0.627035, 0.6, 0.902026);
  }
  100% {
    transform: translateX(160.277782%);
  }
}

@keyframes indeterminate-scale {
  0% {
    transform: scaleX(0.08);
    animation-timing-function: cubic-bezier(
      0.205028,
      0.057051,
      0.57661,
      0.453971
    );
  }
  19.15% {
    transform: scaleX(0.457104);
    animation-timing-function: cubic-bezier(
      0.152313,
      0.196432,
      0.648374,
      1.004315
    );
  }
  44.15% {
    transform: scaleX(0.72796);
    animation-timing-function: cubic-bezier(
      0.257759,
      -0.003163,
      0.211762,
      1.38179
    );
  }
  100% {
    transform: scaleX(0.08);
  }
}
