@import '../settings/variables/transitions';

.ef-collapsible__toggle {
  position: relative;

  &-icon {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
  }
}

.ef-collapsible__toggle[aria-expanded='false'] {
  .ef-collapsible__toggle-icon {
    transform: rotate(0);
  }
}
.ef-collapsible__toggle[aria-expanded='true'] {
  .ef-collapsible__toggle-icon {
    transform: rotate(180deg);
  }
}

.ef-collapsible__toggle[aria-expanded='true'],
.ef-collapsible__toggle[aria-expanded='false'] {
  cursor: pointer;
  user-select: none;

  &.-icon {
    position: relative;
    padding-right: 24px;

    &:after {
      content: '';
      position: absolute;
      top: calc(50% - 12px);
      right: 0;
      width: 16px;
      height: 24px;
      display: inline-block;
      background-size: 16px;
    }

    &.-small {
      padding-right: 20px;

      &:after {
        background-size: 10px;
      }
    }
  }
}

.-inv .ef-collapsible__toggle.-icon:after {
  filter: grayscale(1) brightness(10);
}

.ef-collapsible--has-icon {
  position: relative;
  padding-right: 1.1em;
}

.ef-collapsible__icon {
  position: absolute;
  top: 50%;
  right: 0;
  width: 0.8em;
  height: 0.8em;
  margin-top: -0.4em;
}

// web-ui css-only styles
.ef-collapsible__content:not(.-react) {
  transition: height 0.3s var(--toggle-transition, $toggle-transition),
    margin 0.3s var(--toggle-transition, $toggle-transition),
    padding 0.3s var(--toggle-transition, $toggle-transition),
    opacity 0.3s var(--toggle-transition, $toggle-transition);

  &[aria-hidden='false'] {
    visibility: visible;
  }

  &[aria-hidden='true'] {
    visibility: hidden;
    transition: height 0.3s var(--toggle-transition, $toggle-transition),
      margin 0.3s var(--toggle-transition, $toggle-transition),
      padding 0.3s var(--toggle-transition, $toggle-transition),
      opacity 0.3s var(--toggle-transition, $toggle-transition),
      visibility 0s 0.3s var(--toggle-transition, $toggle-transition);
    opacity: 0;
  }
}
