@import '../settings/mixins/visibility';

.u-hide-visual {
  @include hideVisual();
}

@mixin show-for($size, $range) {
  $lower-bound: map-get($breakpoints, $size);
  $upper-bound: map-get-next($breakpoints, $size);

  @if $range == 'up' {
    @media screen and (max-width: $lower-bound) {
      display: none !important;
    }
  } @else if $range == 'only' {
    @if $lower-bound == 0 {
      @media screen and (min-width: $upper-bound) {
        display: none !important;
      }
    } @else if $upper-bound == null {
      @media screen and (max-width: $lower-bound) {
        display: none !important;
      }
    } @else {
      @media screen and (max-width: $lower-bound),
        screen and (min-width: $upper-bound) {
        display: none !important;
      }
    }
  } @else if $range == 'down' {
    @media screen and (min-width: $upper-bound) {
      display: none !important;
    }
  }
}

@mixin hide-for($size, $range) {
  $lower-bound: map-get($breakpoints, $size);
  $upper-bound: map-get-next($breakpoints, $size);

  @if $range == 'up' {
    @media screen and (min-width: $lower-bound) {
      display: none !important;
    }
  }

  @if $range == 'only' {
    @if $upper-bound == null {
      @media screen and (min-width: $lower-bound) {
        display: none !important;
      }
    } @else {
      @media screen and (min-width: $lower-bound) and (max-width: $upper-bound) {
        display: none !important;
      }
    }
  } @else if $range == 'down' {
    @if $upper-bound == null {
      display: none !important;
    }
    @media screen and (max-width: $upper-bound) {
      display: none !important;
    }
  }
}

// It generates about 180 lines (unminified) of show/hide media queries.
@each $breakpoint, $size in $breakpoints {
  .u-show-#{$breakpoint}-up {
    @include show-for(#{$breakpoint}, up);
  }

  .u-show-#{$breakpoint}-down {
    @include show-for(#{$breakpoint}, down);
  }

  .u-show-#{$breakpoint}-only {
    @include show-for(#{$breakpoint}, only);
  }

  .u-hide-#{$breakpoint}-up {
    @include hide-for(#{$breakpoint}, up);
  }

  .u-hide-#{$breakpoint}-down {
    @include hide-for(#{$breakpoint}, down);
  }

  .u-hide-#{$breakpoint}-only {
    @include hide-for(#{$breakpoint}, only);
  }
}
