@import '../variables/assets';

@mixin iconBG($name, $size: 16px, $white: false) {
  @if ($white) {
    background-image: url('#{$iconsPath}/inverse/#{$name}-white.svg');
  } @else {
    background-image: url('#{$iconsPath}#{$name}.svg');
  }
  background-size: $size;
  background-repeat: no-repeat;
  background-position: 50%;
}
