html[dir='rtl'] {
  .ef-progress__bar.-indeterminate {
    .ef-progress__bar__indicator {
      animation: indeterminate-translate 2s linear infinite reverse;
    }

    .ef-progress__bar__indicator__inner {
      animation: indeterminate-scale 2s linear infinite reverse;
    }
  }

  .ef-progress__bar__indicator {
    transform-origin: top right;
  }

  .ef-progress.-label-inline {
    > * + * {
      margin-left: 0;
      margin-right: 8px;
    }
  }
}
