@import './faces/fonts-latin';
@import './variables/font-weights';
@import './variables/stacks';
@import '../settings/variables/color';
@import '../settings/variables/spacing';
@import '../settings/variables/breakpoints';
@import '../settings/utils/unit';
@import '../settings/mixins/breakpoints';

$text-max-width: 65ch !default;

:root {
  --latin-stack: #{$latin-stack};
}

[lang='en'],
html,
body,
button,
input,
select,
textarea {
  font-family: var(--latin-stack, $latin-stack);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
.ef-h1,
.ef-h2,
.ef-h3,
.ef-h4,
.ef-h5,
.ef-h6 {
  max-width: $text-max-width;
  font-weight: var(--fw-bold, $fw-bold);
  color: var(--color-text, $color-text);
}

h1,
.ef-h1 {
  margin-bottom: pad(m);
  font-size: 56px;
  line-height: 1.143;

  @include breakpoint(m) {
    font-size: 80px;
    line-height: 1.1;
  }
}

h2,
.ef-h2 {
  margin-bottom: pad(m);
  font-size: 40px;
  line-height: 1.2;

  @include breakpoint(m) {
    font-size: 64px;
    line-height: 1.125;
  }
}

h3,
.ef-h3 {
  margin-bottom: pad(m);
  font-size: 32px;
  line-height: 1.25;

  @include breakpoint(m) {
    font-size: 48px;
    line-height: 1.167;
  }
}

h4,
.ef-h4 {
  margin-bottom: pad(m);
  font-size: 24px;
  line-height: 1.333;

  @include breakpoint(m) {
    font-size: 32px;
    line-height: 1.25;
  }
}

h5,
.ef-h5 {
  margin-bottom: pad(m);
  font-size: 20px;
  line-height: 1.2;

  @include breakpoint(m) {
    font-size: 24px;
    line-height: 1.333;
  }
}

h6,
.ef-h6 {
  margin-bottom: pad(m);
  font-size: 16px;
  line-height: 1.5;
}

p,
.ef-text-para,
.ef-text-body {
  max-width: $text-max-width;
  font-size: 16px;
  line-height: 1.5;
  font-weight: var(--fw-book, $fw-book);
  margin-bottom: pad(m);
  color: var(--color-text, $color-text);

  &:last-child {
    margin-bottom: 0;
  }

  &.-light {
    font-weight: var(--fw-light, $fw-light);
  }
}

.ef-text-subtitle {
  font-weight: var(--fw-book, $fw-book);
  font-size: 24px;
  line-height: 1.333;
  margin-bottom: pad(m);
}

.ef-text-ui {
  font-size: 14px;
  line-height: 1.714;
  font-weight: var(--fw-book, $fw-book);

  &.-light {
    font-weight: var(--fw-light, $fw-light);
  }

  &.-bold {
    font-weight: var(--fw-bold, $fw-bold);
  }
}

.ef-text-caption {
  font-weight: var(--fw-book, $fw-book);
  font-size: 12px;
  line-height: 1.333;
}
