@import '../settings/mixins/scrim-gradient';
@import '../settings/variables/color';

%fullbleed-gradient {
  content: '';
  position: absolute;
  right: 0;
  bottom: -1px;
  left: 0;
  height: 50%;
  display: block;

  @include scrim-gradient($color-ink-black, 'to top');
}

.ef-fullbleed {
  position: relative;
  min-height: 60vh;

  &.-gradient {
    .ef-fullbleed__bg:before {
      @extend %fullbleed-gradient;
      opacity: 0.7;
    }
  }

  &.-full-height {
    min-height: 100vh;
  }

  &.-gradient-light .ef-fullbleed__bg:before {
    @extend %fullbleed-gradient;
    opacity: 0.1;
  }

  &.-gradient-midlight .ef-fullbleed__bg:before {
    @extend %fullbleed-gradient;
    opacity: 0.3;
  }

  &.-gradient-mid .ef-fullbleed__bg:before {
    @extend %fullbleed-gradient;
    opacity: 0.5;
  }

  &.-gradient-dark .ef-fullbleed__bg:before {
    @extend %fullbleed-gradient;
    opacity: 1;
  }
}

.ef-fullbleed__bg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
}

.ef-fullbleed__content {
  position: relative;
}
