@import '../settings/variables/transitions';

.ef-icon {
  width: 1em;
  height: 1em;
  display: inline-block;
  svg {
    display: block;
    height: 1em;
    width: 100%;
  }

  &.-light,
  &.-white {
    filter: grayscale(1) brightness(10);
  }
}

.ef-icon {
  &.-animated-chevron {
    width: 20px;
    height: 20px;
    margin: 0 auto;
    transform: translateY(25%);
    display: block;
    fill: currentColor;
    transition: transform $menu-transition var(--menu-transition-time);

    rect {
      transition: transform 0.15s cubic-bezier(0.5, 0, 0.8, 0.7);
      transform-origin: 50%;
    }

    .line1 {
      transform: rotate(45deg);
    }

    .line2 {
      transform: rotate(-45deg);
    }

    .-active & {
      transform: translateY(-25%);

      .line1 {
        transform: rotate(-45deg);
      }

      .line2 {
        transform: rotate(45deg);
      }
    }
  }

  &.-animated-menu {
    $transition-duration: 0.3;
    $transition-delay: (0.5 * $transition-duration);
    $transition: cubic-bezier(0.5, 0, 0.8, 1);
    width: 16px;
    height: 16px;
    transform-origin: 50% 50%;

    * {
      transition: transform $transition-delay + s $transition;
    }

    .line1,
    .rect1 {
      transform-origin: 16px 10.5px;
    }
    .line2,
    .rect2 {
      transform-origin: 16px 21.5px;
    }
    .line1,
    .line2 {
      transition-delay: $transition-delay + s;
    }
    .rect1,
    .rect2 {
      transition-delay: 0s;
    }

    .-active & {
      .line1,
      .line2 {
        transition-delay: 0s;
      }
      .rect1,
      .rect2 {
        transition-delay: $transition-delay + s;
      }
      .line1 {
        transform: translateY(5.5px);
      }
      .rect1 {
        transform: rotate(45deg);
      }
      .line2 {
        transform: translate(0, -5.5px);
      }
      .rect2 {
        transform: rotate(-45deg);
      }
    }
  }
}

.ef-svg-link,
.ef-svg-link:hover,
.ef-svg-link:focus,
.ef-svg-link:active {
  color: var(--color-text);
}

.ef-svg {
  fill: currentColor;
}
