// Set the number of columns you want to use on your layout.
$grid-columns: 12;

// Set the gutter between columns.
$col-gutters: (
    s: 16,
    m: 16,
    l: 24,
    xl: 24
);

// Inner wrapper margin and max-width
$container-settings: (
    s: (
        marginX: 24px,
        maxWidth: none
    ),
    m: (
        marginX: 32px,
        maxWidth: none
    ),
    l: (
        marginX: auto,
        maxWidth: 960px
    ),
    xl: (
        marginX: auto,
        maxWidth: 1200px
    )
);

@function containerMarginX($size: m) {
    @if map_has_key($container-settings, $size) {
        @return map-deep-get($container-settings, $size, "marginX");
    } @else {
        @warn 'Key #{$size} doesn\'t exist for map $container-settings';
    }
}
