@import '../../settings/mixins/icons';
@import '../../settings/mixins/breakpoints';
@import '../../settings/variables/breakpoints';
@import '../../settings/variables/spacing';

html[dir='rtl'] {
  .u-text-left {
    text-align: right;
  }

  .u-text-right {
    text-align: left;
  }

  // Reverse left/right utils
  @each $breakpoint, $bpSize in $breakpoints {
    @include breakpoint($breakpoint) {
      .u-#{$breakpoint}-pr-0 {
        padding-left: 0;
      }

      .u-#{$breakpoint}-pl-0 {
        padding-right: 0;
      }

      .u-#{$breakpoint}-mr-0 {
        margin-left: 0;
      }

      .u-#{$breakpoint}-ml-0 {
        margin-right: 0;
      }

      @each $size in map-keys($spacing-scale) {
        .u-#{$breakpoint}-pr-#{$size} {
          padding-left: pad($size);
        }

        .u-#{$breakpoint}-pl-#{$size} {
          padding-right: pad($size);
        }

        .u-#{$breakpoint}-mr-#{$size} {
          margin-left: pad($size);
        }

        .u-#{$breakpoint}-ml-#{$size} {
          margin-right: pad($size);
        }
      }
    }
  }

  // simple universal version
  @each $size in map-keys($spacing-scale) {
    .u-pr-0 {
      @extend .u-s-pr-0;
    }

    .u-pl-0 {
      @extend .u-s-pl-0;
    }

    .u-mr-0 {
      @extend .u-s-mr-0;
    }

    .u-ml-0 {
      @extend .u-s-ml-0;
    }

    .u-pr-#{$size} {
      @extend .u-s-pr-#{$size};
    }

    .u-pl-#{$size} {
      @extend .u-s-pl-#{$size};
    }

    .u-mr-#{$size} {
      @extend .u-s-mr-#{$size};
    }

    .u-ml-#{$size} {
      @extend .u-s-ml-#{$size};
    }
  }
}
