/**
* Color declarations
**/

// keeping team color naming - we can shorten them
$color-education-blue: #003c64;
$color-first-blue: #009eeb;
$color-hello-pink: #ff329b;
$color-ef-grey: #efefef;
$color-ink-black: #191919;
$color-white: #ffffff;

// paper
$color-first-paper: #e4f7ff;
$color-legal-paper: #fffde6;
$color-hello-paper: #ffe9f4;
$color-education-paper: #e4eaee;
$color-outdoor-paper: #e5ffe5;
$color-ef-paper: #f7f7f7;

$color-popup: #ffffff;

$c-card--outline-color: rgba(25, 25, 25, 0.2);

$color-primary: $color-first-blue !default;
$color-text: $color-ink-black !default;
$color-text--disabled: lighten($color-ink-black, 50%) !default;
$color-text--light: white !default;
$color-text--border: rgba($color-ink-black, 0.4) !default;
$color-link: rgba($color-ink-black, 0.8) !default;
$color-link-bg: #e4f7ff !default;
$color-link-bg-inv: $color-primary !default;
$color-ui-accent: $color-primary !default; // used for interactive form colours, e.g. focus outline
$color-notification-success: #5da335 !default;
$color-notification-warning: #d1334a !default;

// borders
$color-border-interactive: $color-text;
$color-border-interactive--light: $color-text--light;

$focus-shadow: 0 0 0 0.25em rgba($color-border-interactive, 0.1);
$focus-shadow-color: 0 0 0 0.25em rgba($color-primary, 0.3);
$focus-shadow--inv: 0 0 0 0.25em rgba($color-white, 0.15);
