@import '../../settings/mixins/breakpoints';

html[dir='rtl'] {
  .ef-footer__link {
    &[class*='-flag-']:before {
      margin-right: 0;
      margin-left: pad(xs);
    }
  }

  .ef-footer__colophon {
    @include breakpoint(m) {
      .ef-list__item {
        &:not(:first-child) {
          margin-left: 0;
          margin-right: pad(m);
        }

        &:last-child {
          margin-left: 0;
          margin-right: 70px;
        }
      }
    }
  }

  .ef-footer__social-link {
    margin-left: 0;
    margin-right: pad(s);
  }
}
