@import '../settings/variables/buttons';
@import '../settings/variables/spacing';
@import '../settings/variables/buttons';
@import '../settings/variables/general';
@import '../settings/mixins/spinner';
@import '../settings/mixins/breakpoints';
@import '../settings/mixins/common';
@import '../typography/variables/font-weights';

@mixin primaryButton($bg: $btn-primary-bg, $color: $btn-primary-color, $shadowColor: $btn-primary-shadow-color) {
    color: $color;
    background: $bg;
    box-shadow: 0 2px 8px 0 $shadowColor;
    color: var(--btn-primary-color, $color);
    background: var(--btn-primary-bg, $bg);
    box-shadow: var(--btn-primary-box-shadow, 0 2px 8px 0 $shadowColor);
    padding: 16px 32px;

    &.-small {
        padding: 8px 12px;
    }

    &.-large {
        padding: 20px 40px;
    }

    &:hover,
    &.-hover,
    &:focus,
    &.-focus {
        box-shadow: 0 2px 12px 0 $shadowColor;
        box-shadow: var(--btn-primary-box-shadow--hover, 0 2px 12px 0 $shadowColor);
    }

    &.-focus-keyboard {
        box-shadow: 0 0 0 0.25em rgba($shadowColor, 0.3);
        box-shadow: var(--btn-primary-focus-keyboard, $btn-focus-ring--color);
    }

    &[disabled],
    &:disabled,
    &.-is-disabled,
    &.-is-loading {
        opacity: 0.4;
        box-shadow: none;
    }
}

.ef-button-group {
    margin: -#{pad(xs)} 0 0 -#{pad(xs)};
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;

    .ef-button {
        margin: #{pad(xs)} 0 0 #{pad(xs)};

        &:active,
        &:focus {
            z-index: 1;
        }
    }

    &.-condensed {
        margin: -#{pad(xs)} 0 0 0;
        .ef-button {
            margin: #{pad(xs)} 0 0 0;

            &:not(:first-child):not(:last-child) {
                border-radius: 0;
            }

            &:first-child {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }

            &:last-child {
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
            }

            &:not(.-filled):not(:first-child) {
                border-left: none;
            }
        }
    }
}

// General
.ef-button {
    position: relative;
    height: 48px;
    display: inline-flex;
    justify-content: center;
    font-weight: $fw-book;
    font-size: 14px;
    line-height: 16px;
    border: none;
    background-color: transparent;
    border-radius: 24px;
    transition: all 0.15s ease;
    cursor: pointer;
    user-select: none;
    outline: none;
    white-space: nowrap;
    text-overflow: ellipsis;

    * {
        pointer-events: none; // ensure child elements don't get focus
    }

    &.-small {
        height: 32px;
        font-size: 12px;
        border-radius: 15px;
    }

    &.-large {
        height: 56px;
        padding-top: 20px;
        padding-bottom: 20px;
        border-radius: 28px;
    }

    &.-square {
        border-radius: 4px;
    }

    // Default states
    &[disabled],
    &:disabled,
    &.-is-disabled {
        pointer-events: none;
    }

    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
    }

    &.-is-loading {
        @include loadingSpinner();

        .ef-button__content {
            visibility: hidden;
        }
    }
}

.ef-button.-primary {
    @include primaryButton()
}

// Outlined, formerly 'secondary'
// todo v3: remove -secondary class

.ef-button.-secondary,
.ef-button.-outlined {
    color: $btn-outlined-color;
    border: 1px solid $btn-outlined-border-color;
    color: var(--btn-outlined-color, $btn-outlined-color);
    border: 1px solid var(--btn-outlined-border-color, $btn-outlined-border-color);
    background: transparent;
    box-shadow: var(--btn-outlined-box-shadow, 0 0 0 0 rgba($btn-outlined-shadow-color, 0));

    &:hover,
    &.-hover,
    &:focus,
    &.-focus {
        border: 1px solid $btn-outlined-border-color--hover;
        box-shadow: 0 3px 8px 0 rgba($btn-outlined-shadow-color, 0.24);
        border: 1px solid var(--btn-outlined-border-color--hover, $btn-outlined-border-color--hover);
        box-shadow: var(--btn-outlined-box-shadow--hover, 0 3px 8px 0 rgba($btn-outlined-shadow-color, 0.24));
    }

    &[disabled],
    &:disabled,
    &.-is-disabled,
    &.-is-loading {
        color: $btn-outlined-color--disabled;
        border: 1px solid $btn-outlined-border-color--disabled;
        color: var(--btn-outlined-color--disabled, $btn-outlined-color--disabled);
        border: 1px solid var(--btn-outlined-border-color--disabled, $btn-outlined-border-color--disabled);
        box-shadow: 0 0 0 0 rgba($btn-outlined-shadow-color, 0);
    }

    // Dark bg styles
    .-inv &,
    .-has-dark-bg &,
    &.-on-dark {
        color: var(--btn-outlined-on-dark-color, $btn-outlined-on-dark-color);
        border: 1px solid var(--btn-outlined-on-dark-border-color, $btn-outlined-on-dark-border-color);
        box-shadow: none;

        &:hover,
        &.-hover,
        &:focus,
        &.-focus
        {
            color: $btn-outlined-on-dark-color--hover;
            background: $btn-outlined-on-dark-bg--hover;
            background: var(--btn-outlined-on-dark-bg--hover, $btn-outlined-on-dark-bg--hover);
            color: $btn-outlined-on-dark-color--hover;
            border: 1px solid transparent;
            box-shadow: none;
        }

        &.-focus-keyboard {
            color: $btn-outlined-on-dark-color--hover;
            background: $btn-outlined-on-dark-bg--hover;
            color: $btn-outlined-on-dark-color--hover;
            background: var(--btn-outlined-on-dark-bg--hover, $btn-outlined-on-dark-bg--hover);
            box-shadow: var(--btn-mono-on-dark--focus-keyboard, $btn-focus-ring--mono-on-dark);
        }
    }
}

// todo v3: remove -secondary class
.ef-button.-secondary.-filled,
.ef-button.-filled {
    border: 1px solid transparent;
    color: $btn-filled-color;
    background: $btn-filled-bg;
    box-shadow:  0 2px 8px 0 rgba($btn-filled-shadow-color, 0.2);
    color: var(--btn-filled-color, $btn-filled-color);
    background: var(--btn-filled-bg, $btn-filled-bg);
    box-shadow: var(--btn-filled-box-shadow, 0 2px 8px 0 rgba($btn-filled-shadow-color, 0.2));

    &:hover,
    &.-hover,
    &:hover,
    &.-hover {
         border: 1px solid transparent;
         box-shadow: 0 3px 8px 0 rgba($btn-filled-shadow-color, 0.24);
         box-shadow: var(--btn-filled-box-shadow--hover, 0 3px 8px 0 rgba($btn-filled-shadow-color, 0.24));

        .-inv &,
        .-has-dark-bg &,
        &.-on-dark {
            color: $btn-filled-on-dark-color--hover;
            background: $btn-filled-on-dark-bg--hover;
            box-shadow: 0 2px 12px 0 $btn-filled-on-dark-shadow-color--hover;
            color: var(--btn-filled-on-dark-color--hover, var(--btn-primary-color, 0 2px 12px 0 $btn-filled-on-dark-color--hover));
            background: var(--btn-filled-on-dark-bg--hover, var(--btn-primary-bg, $btn-filled-on-dark-bg--hover));
            box-shadow: var(--btn-filled-on-dark-box-shadow--hover, var(--btn-primary-box-shadow, 0 2px 12px 0 $btn-filled-on-dark-shadow-color--hover));
        }
     }

    &[disabled],
    &:disabled,
    &.-is-disabled,
    &.-is-loading {
        border: 1px solid transparent;
        color: $btn-filled-color--disabled;
        box-shadow: 0 2px 4px rgba($btn-filled-shadow-color, 0.1);
        color: var(--btn-filled-color--disabled, $btn-filled-color--disabled);
        box-shadow: var(--btn-filled-box-shadow--disabled, 0 2px 4px rgba($btn-filled-shadow-color, 0.1));

        .-inv &,
        .-has-dark-bg &,
        &.-on-dark {
            color: $btn-filled-color--disabled;
            background: rgba($btn-filled-bg, 0.8);
            box-shadow:  0 0 0 0 rgba($btn-filled-shadow-color, 0);
        }
    }
}

.ef-button.-secondary,
.ef-button.-outlined,
.ef-button.-filled {
    padding: 15px 32px;

    &.-small {
        padding: 7px 12px;
    }

    &.-large {
        padding: 19px 40px;
    }

    &.-focus-keyboard {
        box-shadow: var(--btn-focus-ring--mono-on-light, $btn-focus-ring--mono-on-light);
    }

    .-inv &,
    .-has-dark-bg &,
    &.-on-dark {
        &.-focus-keyboard {
            box-shadow: var(--btn-focus-ring--mono-on-dark, $btn-focus-ring--mono-on-dark);
        }
    }
}

a.ef-button,
.-inv a.ef-button {
    &,
    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
    }
}

.ef-button__content {
    display: inline-flex;
    align-items: center;

    * + * {
        margin-left: pad(xs);
    }
}

.ef-button.-icon {
    padding: pad(xxs) 0;
    color: var(--color-text);
    align-items: center;

    .ef-button__icon {
        width: 32px;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        color: $btn-filled-color;
        background: $btn-filled-bg;
        box-shadow:  0 2px 8px 0 rgba($btn-filled-shadow-color, 0.2);
        color: var(--btn-filled-color, $btn-filled-color);
        background: var(--btn-filled-bg, $btn-filled-bg);
        box-shadow: var(--btn-filled-box-shadow, 0 2px 8px 0 rgba($btn-filled-shadow-color, 0.2));
    }

    &.-m {
        padding: 0;
        .ef-button__icon {
            width: 48px;
            height: 48px;
        }
    }
    &.-l {
        padding: 0;
        .ef-button__icon {
            width: 56px;
            height: 56px;
        }
    }
    &.-xl {
        padding: 0;
        .ef-button__icon {
            width: 72px;
            height: 72px;
        }
    }

    &:hover,
    &.-hover {
        .ef-button__icon {
            box-shadow: var(--btn-filled-box-shadow--hover, 0 3px 8px 0 rgba($btn-filled-shadow-color, 0.24));
        }
    }

    &[disabled],
    &:disabled,
    &.-is-disabled {
        .ef-button__icon {
            color: $btn-filled-color--disabled;
            box-shadow: 0 2px 4px rgba($btn-filled-shadow-color, 0.1);
            color: var(--btn-filled-color--disabled, $btn-filled-color--disabled);
            box-shadow: var(--btn-filled-box-shadow--disabled,  0 2px 4px rgba($btn-filled-shadow-color, 0.1));
        }
    }
}

@include breakpoint(s only) {
    .ef-input-w .ef-button,
    .ef-button.-s-extended,
    .ef-button.-s-half {
        width: 100%;
        height: 56px;
        padding-top: 20px;
        padding-bottom: 20px;
        border-radius: 28px;
        @include inputMaxWidth();
    }

    .ef-button.-s-half {
        width: calc(50% - 8px);
    }
}
