html[dir='rtl'] {
  select.ef-input {
    padding-right: pad(s);
    padding-left: pad(l);
  }

  .ef-input-w.-select:after {
    margin-left: unset;
    margin-right: -#{pad(l)};
  }

  .ef-input-w:not(.ef-boolean).-is-valid,
  .ef-input-w:not(.ef-boolean).-is-invalid,
  .ef-input-w.-is-loading {
    .ef-input {
      padding-right: pad(s);
    }
  }

  .ef-input-w:not(.ef-boolean).-is-valid,
  .ef-input-w:not(.ef-boolean).-is-invalid {
    &:after {
      margin-left: unset;
      margin-right: -#{pad(l)};
    }
  }

  .ef-input-w.-is-loading:after {
    left: 12px;
    right: auto;
  }

  .ef-boolean__element.-switch:before {
    transform: translateX(-32px);
  }

  .ef-boolean__input:checked ~ .-switch:before {
    transform: translateX(0);
  }
}
