@import './variables/stacks';
@import './faces/fonts-latin-extended';
@import './faces/fonts-latin';

:root {
  --latin-extended-stack: #{$latin-extended-stack};
}

[lang='wwru'],
[lang='ru'],
[lang='kz'] {
  &,
  body,
  button,
  input,
  select,
  textarea {
    font-family: var(--latin-extended-stack, $latin-extended-stack);
  }
}
