@import '../typography/variables/font-weights';
@import '../settings/variables/color';

.ef-table-w {
  overflow-y: auto;
}

.ef-table {
  table-layout: fixed;
  min-width: 100%;
  font-size: 12px;
  text-align: left;
  border-collapse: collapse;

  &.-layout-auto {
    table-layout: auto;
  }

  &.-align-cells-top tr {
    vertical-align: top;
  }

  th {
    font-weight: bold;
  }

  th,
  td {
    padding: pad(xs);
    color: var(--color-text, $color-text);
    white-space: normal;
  }

  th {
    border-bottom: 2px solid var(--color-ui-border);
  }

  td {
    border-bottom: 1px solid var(--color-ui-border);
  }

  tbody td:first-child,
  tbody th:first-child {
    font-weight: var(--fw-bold, $fw-bold);
  }

  td:last-child,
  th:last-child {
    padding-right: 0;
  }

  td:first-child,
  th:first-child {
    padding-left: 0;
  }

  tfoot tr td {
    font-size: 11px;
  }
}
