@import '../settings/variables/color';

:root {
    --color-education-blue: #{$color-education-blue};
    --color-first-blue: #{$color-first-blue};
    --color-hello-pink: #{$color-hello-pink};
    --color-ef-grey: #{$color-ef-grey};
    --color-ink-black: #{$color-ink-black};
    --color-white: #{$color-white};

    // paper
    --color-first-paper: #{$color-first-paper};
    --color-legal-paper: #{$color-legal-paper};
    --color-hello-paper: #{$color-hello-paper};
    --color-education-paper: #{$color-education-paper};
    --color-outdoor-paper: #{$color-outdoor-paper};
    --color-ef-paper: #{$color-ef-paper};

    --color-popup: #{$color-popup};

    --c-card--outline-color: #{$c-card--outline-color};

    --color-primary: #{$color-primary};
    --color-text: #{$color-text};
    --color-text--disabled: #{$color-text--disabled};
    --color-text--light: #{$color-text--light};
    --color-text--border: #{$color-text--border};
    --color-link: #{$color-link};
    --color-link--active: #{$color-text};
    --color-link-bg: #{$color-link-bg};
    --color-link-bg-inv: #{$color-link-bg-inv};
    --color-ui-accent: #{$color-ui-accent};
    --color-notification-success: #{$color-notification-success};
    --color-notification-warning: #{$color-notification-warning};

    --color-border-interactive: #{$color-border-interactive};
    --color-border-interactive--light: #{$color-border-interactive--light};

    --focus-shadow: #{$focus-shadow};
    --focus-shadow-color: #{$focus-shadow-color};
    --focus-shadow--inv: #{$focus-shadow--inv};

    // text
    --color-text--inactive: #{$color-text--disabled};
    --color-text--inverse: #{$color-text--light};


    // inputs
    --input-bg: #{$color-white};
    --input-bg--disabled: #{rgba($color-text, 0.05)};
    --input-border: #{rgba($color-border-interactive, 0.5)};
    --input-border--disabled: #{rgba($color-border-interactive, 0.1)};
    --input-shadow: 0 2px 8px #{rgba($color-border-interactive, 0.1)};

    // focus state
    --focus--box-shadow: #{$focus-shadow};

    // nav
    --ef-nav--color: var(--color-link, $color-link);
    --ef-nav--color--active: var(--color-link-active, $color-text);
    --ef-nav--border-color: var(--color-text--border, $color-text--border);

    // border
    --color-ui-border: #{rgba($color-border-interactive, 0.3)};

    .-inv {
        // text
        --color-text: #{$color-white};
        --color-text--inactive: #{rgba($color-white, 0.6)};
        --color-text--inverse: #{$color-ink-black};
        --color-text--border: #{rgba($color-white, 0.4)};

        // link
        --color-link: #{rgba($color-white, 0.8)};
        --color-link--active: #{$color-white};
        --color-link-bg: #{$color-link-bg-inv};

        // inputs
        --input-bg: #{rgba($color-white, 0.9)};
        --input-bg--disabled: #{rgba($color-white, 0.2)};
        --input-border: transparent;
        --input-border--disabled: transparent;

        // focus state
        --focus--box-shadow: #{$focus-shadow--inv};

        // border
        --color-ui-border: #{rgba($color-border-interactive--light, 0.4)};
    }
}
