//
// Select inputs
//
@import '../settings/mixins/icons';
@import '../settings/variables/color';

select.ef-input {
  appearance: none;
  // fixes for IE
  &:focus::-ms-value {
    color: $color-ui-accent;
    background-color: $color-white;
  }
}

.ef-input-w {
  &.-select {
    &:after {
      content: '';
      display: block;
      width: 16px;
      height: 16px;
      margin-left: -32px;
      @include iconBG(chevron-down);
      pointer-events: none;
    }
    &.-is-disabled:after {
      opacity: 0.3;
    }
  }
}

select.ef-input {
  padding-right: 32px;

  // workaround for 'placeholder'
  &:invalid {
    color: $color-text--disabled;
  }
}
