@import '../settings/mixins/breakpoints';
@import '../settings/mixins/grid';
@import '../settings/mixins/scrim-gradient';
@import '../settings/mixins/resets';
@import '../settings/variables/color';
@import '../settings/variables/transitions';
@import '../base/defaults';

$ef-top-header-height-mobile: 112px;
$ef-top-header-height-mobile--condensed: 64px;

@mixin headerShadow() {
  content: '';
  position: absolute;
  top: 100%;
  width: 100%;
  height: 4px;
  display: block;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.15),
    rgba(0, 0, 0, 0)
  );
}

.ef-header {
  --ef-header--color: #{$body-text-color};
  --ef-header--lighter-color: #474747;
  --ef-header__top--border-bottom: 1px solid rgba(25, 25, 25, 0.1);
  color: var(--ef-header--color);
  position: relative;
  width: 100%;
  z-index: 9999;
  font-weight: normal;

  &:after {
    content: '';
    position: fixed;
    top: -100vh;
    right: 0;
    left: 0;
    height: 100vh;
    z-index: -1;
    transform: scaleY(0);
    display: block;
    visibility: hidden;
    opacity: 0;
    background: white;
    transition: opacity 0.6s ease-out,
      transform var(--menu-transition-time, $menu-transition-time)
        cubic-bezier(0.04, 0.04, 0.12, 0.96);
    pointer-events: none;
  }

  &.-ef-nav--is-open {
    &:after {
      transform: translateY(100vh);
      visibility: visible;
      opacity: 1;
    }

    .ef-header__logo,
    .ef-header__icon {
      filter: none;
    }

    .ef-header__icon {
      transition: opacity 0.2s ease-out;
      &:hover {
        opacity: 0.9;
      }
    }
  }

  &.-sticky:not(.-global):not(.-condensed),
  &.-is-offset {
    &.-ef-nav--is-open {
      &:after {
        height: calc(100vh + 48px);
      }
    }
  }

  &:not(.-transparent) {
    background-color: white;

    .ef-header__main:after {
      @include headerShadow;
    }
  }

  &.-transparent {
    --ef-header--color: #fff;
    --ef-header--lighter-color: #fff;
    --ef-header--icon-filter: invert(1) brightness(10);
    --ef-header__top--border-bottom: rgba(
      var(--color-white, $color-white),
      0.4
    );
    --ef-nav--color: rgba(var(--color-white, $color-white), 0.8);
    --ef-nav--color--active: var(--color-white, $color-text);
    --ef-nav--border-color: rgba(var(--color-white, $color-white), 0.4);
    position: relative;
    z-index: 10;

    &.-ef-nav--is-open {
      &:after {
        opacity: 1;
        height: calc(100vh);
      }
    }

    & + .ef-fullbleed {
      margin-top: -112px;

      @include breakpoint(m) {
        margin-top: -200px;
      }
    }

    &.-condensed {
      & + .ef-fullbleed {
        margin-top: -64px;

        @include breakpoint(m) {
          margin-top: -80px;
        }
      }
    }
  }

  &.-transparent:not(.-ef-nav--is-open):not(.-sticky) {
    &.-sticky {
      .ef-header__main:after {
        @include headerShadow;
      }
    }
  }

  &.-global {
    &:not(.-ef-nav--is-open) {
      .ef-header__group.-global {
        &:after {
          border-bottom: none;
          @include headerShadow;
        }
      }
    }
  }

  &[class*='-gradient'] {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      height: 112px;
      display: block;
      opacity: 0.7;
      z-index: -1;
      @include scrim-gradient(#000, 'to bottom');

      @include breakpoint(m) {
        height: 192px;
      }
    }

    &.-condensed {
      &:before {
        height: 64px;

        @include breakpoint(m) {
          height: 124px;
        }
      }
    }
  }

  &.-gradient-mid:before {
    opacity: 0.5;
  }

  &.-gradient-midlight:before {
    opacity: 0.3;
  }

  &.-gradient-light:before {
    opacity: 0.1;
  }

  &.-ef-nav--is-open {
    --ef-header--color: #{$body-text-color};
    --ef-header--lighter-color: #474747;
    --ef-header__top--border-bottom: 1px solid rgba(25, 25, 25, 0.1);

    .ef-header__main:after {
      display: none;
    }

    &:before {
      opacity: 0;
    }
  }
}

.ef-header__group {
  &.-global {
    position: relative;
    font-size: 14px;

    .ef-header__item {
      padding: 12px 0;
    }

    .ef-header__group__inner {
      @include container();
    }

    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 0;
      position: absolute;
      bottom: 0;
      border-bottom: var(
        --ef-header__top--border-bottom,
        1px solid rgba(25, 25, 25, 0.1)
      );
    }
  }

  &.-product {
    .ef-header__item {
      padding: 16px 0;

      @include breakpoint(m) {
        padding: 32px 0 16px;

        .ef-header.-condensed & {
          padding: 24px 0;
        }
      }
    }

    .ef-header__group__inner {
      @include container();
    }
  }

  &.-standalone {
    .ef-header__item {
      padding: 16px 0;

      @include breakpoint(m) {
        padding: 24px 0;
      }
    }
  }

  &.-nav {
    position: relative;

    @include breakpoint(m) {
      .ef-header__item {
        padding: 12px 0 0;
      }

      .ef-nav__link {
        padding-bottom: 12px;
      }
    }

    @include breakpoint(m) {
      .ef-header__group__inner {
        @include container();
      }
    }
  }
}

.ef-header__group__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ef-header__col {
  flex: 1;
  color: var(--ef-header--color, $color-text);

  &.-center {
    display: flex;
    justify-content: center;
  }

  &.-right {
    text-align: right;
  }
}

.ef-header__title {
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 1.231;
  font-size: 13px;
  font-weight: 900;
  color: var(--ef-header--color, $color-text);

  @include breakpoint(m) {
    font-size: 18px;
    line-height: 26px;
  }
}

.ef-header__tagline {
  margin: 0;
  line-height: 1.231;
  font-size: 13px;
  font-weight: normal;
  color: var(--ef-header--lighter-color, #474747);

  @include breakpoint(m) {
    line-height: 1.385;
  }
}

.ef-header__top-link {
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  color: var(--ef-nav--color, $color-link);

  &:hover,
  &:focus {
    color: var(--ef-nav--color, $color-link);
    text-decoration: underline var(--ef-nav--border-color, $color-text--border);
  }

  &:active {
    color: var(--ef-nav--color--active, $color-text);
    text-decoration: underline var(--ef-nav--border-color, $color-text--border);
  }
}

.ef-header__top-link-text {
  @include breakpoint(s only) {
    display: none;
  }
}

.ef-header__ctas {
  display: flex;
  align-items: flex-end;

  @include breakpoint(s only) {
    display: none;
  }
}

.ef-header__menu-toggle {
  @include reset-button();
  transition: transform var(--menu-transition-time, $menu-transition-time);
  width: 32px;
  height: 32px;
  margin-right: -8px;
  display: flex;
  justify-content: center;
  align-items: center;

  &.-active .-c {
    transform: rotateX(180deg);
  }

  .ef-header__col.-left & {
    margin-right: 0;
    margin-left: -8px;
  }

  @include breakpoint(m) {
    display: none;
  }
}

.ef-header__icon {
  filter: var(--ef-header--icon-filter, none);
  font-size: 16px;

  &.-ml {
    margin-left: 12px;
  }
  &.-mr {
    margin-right: 12px;
  }
}

.ef-header__logo {
  filter: var(--ef-header--icon-filter, none);
  height: 32px;
  color: var(--color-text, #{$color-ink-black});

  &:hover,
  &:active,
  &:focus {
    color: var(--color-text, #{$color-ink-black});
  }
}

// sticky headers
.ef-header.-sticky {
  --ef-header--color: #{$body-text-color};
  --ef-header--lighter-color: #474747;
  --ef-header__top--border-bottom: inset 0 0 0 1px rgba(25, 25, 25, 0.1);
  --ef-header--icon-filter: none;
  position: sticky;
  background-color: white;
  top: -48px;
  transform: translate3d(0, -64px, 0);

  &:not(.-ef-nav--is-open) {
    .ef-header__main:after {
      @include headerShadow;
    }
  }

  &.-sticky-show {
    transform: translate3d(0, 0, 0);
    transition: transform 0.2s ease-out;
  }

  &.-sticky-hide {
    transform: translate3d(0, -64px, 0);
    transition: transform 0.2s ease-out;
  }

  &.-condensed,
  &.-global {
    top: 0;
  }

  &:before {
    display: none;
  }
}

// condensed
.ef-header.-condensed {
  .ef-header__group.-standalone {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include breakpoint(s only) {
      flex-wrap: wrap;
    }
  }

  .ef-header__group.-nav {
    @include breakpoint(s only) {
      $margin: containerMarginX(s);
      width: calc(100% + #{(2 * $margin)});
      margin: 0 -#{$margin};
    }
  }
}
