@import '../base/color';
@import '../settings/mixins/resets';

.ef-breadcrumbs {
  @include reset-list-style();
  display: flex;
  flex-wrap: wrap;
}

.ef-breadcrumbs__item {
  display: inline-flex;
  margin-right: 8px;
  font-size: 14px;
  font-weight: normal;
  line-height: normal;
  &:after {
    content: '>';
    margin-left: 8px;
    color: var(--color-link, $color-link);
  }

  &:last-child {
    &:after {
      content: '';
    }
  }
}

.ef-breadcrumbs__link {
  color: var(--color-link, $color-link);
  &:hover,
  &:focus {
    color: var(--color-link, $color-link);
    text-decoration: underline var(--color-text--border, $color-text--border);
  }
  &:active {
    color: var(--color-text, $color-text);
  }

  &.-is-active {
    color: var(--color-text, $color-text);
    font-weight: bold;
    text-decoration: none;
  }
}
