@import '../../settings/mixins/breakpoints';

html[dir='rtl'] {
  .ef-modal {
    right: calc(50% - 8px);
    left: 0;
    transform: translate(50%, 0);

    @include breakpoint(m) {
      transform: translate(50%, -50%);
    }
  }

  .ef-modal__dismiss {
    right: auto;
    left: 12px;

    @include breakpoint(m) {
      right: auto;
      left: 20px;
    }
  }

  .ef-modal__overlay.-lightbox .ef-modal__dismiss {
    top: -44px;
    right: auto;
    left: -12px;
  }
}
