@import '../settings/mixins/resets';
@import '../settings/mixins/breakpoints';
@import '../settings/variables/buttons';
@import '../settings/variables/color';
@import '../settings/variables/transitions';

$ef-nav--container-margin--small: 32px;
$ef-nav--links-font-size: 14px;
$ef-nav--links-font-size--mobile: 16px;
$ef-top-header-height-mobile: 112px;
$ef-top-header-height-mobile--condensed: 64px;

:root {
  --ef-nav--container-margin--small: #{$ef-nav--container-margin--small};
  --ef-nav--links-font-size: #{$ef-nav--links-font-size};
  --ef-nav--links-font-size--mobile: #{$ef-nav--links-font-size--mobile};
  --ef-top-header-height-mobile: #{$ef-top-header-height-mobile};
  --ef-top-header-height-mobile--condensed: #{$ef-top-header-height-mobile--condensed};
}

.ef-nav {
  position: relative;
  font-size: var(--ef-nav--links-font-size, $ef-nav--links-font-size);

  @include breakpoint(s only) {
    overflow: hidden;
    position: absolute;
    top: 0;
    width: 100%;
    height: calc(
      100vh - var(--ef-top-header-height-mobile, $ef-top-header-height-mobile)
    );
    z-index: 10;
    transform: translate3d(0, -1em, 0);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.2s var(--menu-transition, $menu-transition),
      transform var(--menu-transition-time, $menu-transition-time)
        var(--menu-transition, $menu-transition),
      visibility var(--menu-transition-time, $menu-transition-time)
        var(--menu-transition, $menu-transition);

    &.-active {
      overflow: auto;
      transform: translate3d(0, 0, 0);
      visibility: visible;
      opacity: 1;
      transition: opacity 0.2s var(--menu-transition, $menu-transition),
        transform var(--menu-transition-time, $menu-transition-time)
          var(--menu-transition, $menu-transition);
    }

    .ef-header.-condensed &,
    .ef-header.-sticky & {
      height: calc(
        100vh -
          var(
            --ef-top-header-height-mobile--condensed,
            $ef-top-header-height-mobile--condensed
          )
      );

      &:after {
        top: -var(
          --ef-top-header-height-mobile--condensed,
          $ef-top-header-height-mobile--condensed
        );
      }
    }
  }
}

.ef-nav__menu {
  @include reset-list-style;

  @include breakpoint(s only) {
    margin: 24px
      var(--ef-nav--container-margin--small, $ef-nav--container-margin--small);
  }
  @include breakpoint(m) {
    display: flex;
  }

  // ensure last item is visible on iOS Safari with bottom bar overlay
  @supports (-webkit-overflow-scrolling: touch) {
    margin-bottom: 44px;
  }
}

.ef-nav__item {
  @include breakpoint(m) {
    display: block;
    margin-right: 32px;
    line-height: 24px;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      .ef-nav__sub-menu {
        display: block;
      }
    }
  }

  &.-is-active {
    .ef-nav__link {
      position: relative;

      @include breakpoint(s only) {
        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 4px;
          height: 100%;
          background: var(--color-primary, $color-primary);
        }
      }
      @include breakpoint(m) {
        color: var(--ef-nav--color--active, $color-text);
      }
    }
  }

  &.-with-sub-menu {
    position: relative;
  }
}

@include breakpoint(s only) {
  .ef-nav__item .ef-collapsible__toggle.ef-nav__sub-menu-toggle {
    position: absolute;
    top: 0;
    right: -28px;
    padding: 0;
    width: 56px;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: center;

    &:after {
      position: static;
    }

    .ef-collapsible__icon {
      position: static;
      padding: 0;
      margin: 0;
      width: 16px;
      height: 16px;
    }
  }
}

.ef-nav__link {
  display: block;

  @include breakpoint(s only) {
    font-size: var(
      --ef-nav--links-font-size--mobile,
      $ef-nav--links-font-size--mobile
    );
    line-height: 1.5;
    margin: 0
      var(--ef-nav--container-margin--small, $ef-nav--container-margin--small);
    padding: 4px 24px 4px
      var(--ef-nav--container-margin--small, $ef-nav--container-margin--small);
    transition: padding var(--menu-transition-time, $menu-transition-time)
      var(--menu-transition, $menu-transition);

    .ef-nav.-active & {
      padding: 16px 24px 16px
        var(--ef-nav--container-margin--small, $ef-nav--container-margin--small);
    }
  }

  @include breakpoint(m) {
    color: var(--ef-nav--color);

    &:hover,
    &:focus,
    &:active {
      color: var(--ef-nav--color);
      text-decoration: underline;
      text-decoration-color: var(--ef-nav--border-color);
    }

    &:active {
      color: var(--ef-nav--color--active, $color-text);
    }
  }
}

.ef-nav__link,
.ef-nav__sub-link {
  &,
  &:hover,
  &:active,
  &:focus {
    @include breakpoint(s only) {
      color: var(--color-text, $color-text);
      text-decoration: none;
    }
  }
}

.ef-nav__sub-menu {
  @include reset-list-style;
  @include breakpoint(m) {
    position: absolute;
    top: calc(100% - 4px);
    left: 50%;
    min-width: 156px;
    transform: translateX(-50%);
    z-index: 10;
    padding: 14px 32px;
    display: none;
    border-radius: 4px;
    background-color: var(--color-popup, $color-popup);
    box-shadow: $box-shadow-dark-enabled;

    &:before {
      content: '';
      position: absolute;
      top: -13px;
      left: 0;
      width: 100%;
      height: 13px;
      display: block;
    }

    .ef-nav__item:first-child & {
      left: -32px;
      transform: none;
    }

    .ef-nav__item:last-child & {
      left: auto;
      right: -32px;
      transform: none;
    }
  }
}

.ef-nav__sub-item {
  @include breakpoint(m) {
    white-space: nowrap;
  }
  &.-is-active {
    .ef-nav__sub-link {
      position: relative;
      font-weight: bold;
      color: inherit;
      @include breakpoint(s only) {
        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 4px;
          height: 100%;
          background: var(--color-primary, $color-primary);
        }
      }
    }
  }
}

.ef-nav__sub-link {
  display: block;
  font-size: var(--ef-nav--links-font-size, $ef-nav--links-font-size);

  @include breakpoint(s only) {
    font-size: var(
      --ef-nav--links-font-size--mobile,
      $ef-nav--links-font-size--mobile
    );
    line-height: 1.5;
    padding: 16px 0 16px 32px;
  }

  @include breakpoint(m) {
    padding: 6px 0;
    font-size: 13px;
  }
}

// mobile call to action buttons
.ef-nav__small-cta {
  @include reset-list-style;
  @extend .u-show-s-only !optional;
  margin-top: 16px;

  & + .ef-nav__small-cta {
    margin-top: 0;

    .ef-button.-secondary {
      border-top: none;
    }
  }

  .ef-button {
    width: calc(100% + 64px);
    padding: 20px 32px;
    justify-content: flex-start;
    border-radius: 0;
    font-size: 16px;
    line-height: 1.5;
    margin: 0 -32px;

    &.-secondary {
      border-top: 1px solid rgba(var(--color-ink-black, $color-ink-black), 0.2);
      border-bottom: 1px solid
        rgba(var(--color-ink-black, $color-ink-black), 0.2);
    }

    &,
    &:hover,
    &:active,
    &:focus,
    &:focus:hover {
      box-shadow: none;
    }
  }
}
