//
// Range slider inputs
//
@import '../settings/variables/color';

.ef-range {
  // The draghandle
  $thumb-color: #fff;
  $thumb-radius: 50%;
  $thumb-height: 32px;
  $thumb-width: 32px;

  // The range
  $track-color: rgba(25, 25, 25, 0.2);
  $track-radius: 19px;
  $track-width: 100%;
  $track-height: 8px;
  --track-color: #{$track-color};
  --track-radius: #{$track-radius};
  --track-width: #{$track-width};
  --track-height: #{$track-height};

  width: 100%; // for edge
  margin: 0;
  padding: 0;
  height: 42px;
  display: block;
  background: transparent;
  -webkit-appearance: none;

  .-inv & {
    --track-color: rgba(255, 255, 255, 0.2);
  }

  @mixin track {
    width: $track-width;
    height: $track-height;
    cursor: pointer;
  }

  @mixin thumb {
    box-sizing: border-box; // FF fix
    width: $thumb-width;
    height: $thumb-height;
    padding: 11px;
    cursor: pointer;
    border: none;
    border-radius: $thumb-radius;
    box-shadow: 0 0 0 2px #fff, 0 0 0 1px transparent, 0 0 0 transparent;
    background: linear-gradient(#fff, #fff) content-box,
      linear-gradient(
          to bottom,
          rgba(#fff, 0.2),
          var(--color-primary, $color-primary)
        )
        border-box,
      var(--color-primary, $color-primary) border-box;
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  @mixin thumb-hover-active {
    transform: scale(1.2);
    box-shadow: 0 0 0 2px #fff, 0 0 0 transparent,
      0 4px 12px 0 rgba(0, 158, 232, 0.2);
  }

  @mixin thumb-focus {
    box-shadow: 0 0 0 2px #fff,
      0 0 0 3px var(--color-education-blue, $color-education-blue),
      0 0 0 transparent;
  }

  @mixin thumb-disabled {
    transform: scale(0.5);
    box-shadow: none;
    background: #8c8c8c;
  }

  &:focus,
  &.-focus {
    outline: none;

    &::-webkit-slider-thumb {
      @include thumb-focus;
    }
    &::-moz-range-thumb {
      @include thumb-focus;
    }
    &::-ms-thumb {
      @include thumb-focus;
    }
  }

  &:active,
  &.-is-active {
    &::-webkit-slider-thumb {
      @include thumb-hover-active;
    }
    &::-moz-range-thumb {
      @include thumb-hover-active;
    }
    &::-ms-thumb {
      @include thumb-hover-active;
    }
  }

  &:disabled {
    pointer-events: none;

    &::-webkit-slider-thumb {
      @include thumb-disabled;
    }
    &::-moz-range-thumb {
      @include thumb-disabled;
    }
    &::-ms-thumb {
      @include thumb-disabled;
    }
  }

  &::-webkit-slider-runnable-track {
    @include track;
    border: none;
    border-radius: $track-radius;
    background: var(--track-color, $track-color);
  }

  &::-webkit-slider-thumb {
    @include thumb;
    -webkit-appearance: none;
    margin-top: ($track-height/ 2)- ($thumb-height / 2);
  }

  &::-moz-range-track {
    @include track;
    border: none;
    border-radius: $track-radius;
    background: var(--track-color, $track-color);
  }

  &::-moz-range-thumb {
    @include thumb;
  }

  &::-ms-track {
    @include track;
    color: transparent;
    border-width: 2px 0;
    border-color: transparent;
    background: transparent;
  }

  &::-ms-fill-lower {
    border: none;
    border-radius: $track-radius * 2;
    background: var(--track-color, $track-color);
  }

  &::-ms-fill-upper {
    border: none;
    border-radius: $track-radius * 2;
    background: var(--track-color, $track-color);
  }

  &::-ms-thumb {
    @include thumb;
    margin: 0 5px;
  }

  &:focus::-ms-fill-lower {
    background: var(--track-color, $track-color);
  }

  &:focus::-ms-fill-upper {
    background: var(--track-color, $track-color);
  }
}
