@import '../settings/variables/spacing';

.u-right-gutter {
  margin-right: 16px;
}

.u-mx-auto {
  margin-left: auto;
  margin-right: auto;
}

// generate responsive spacing utils
@each $breakpoint, $bpSize in $breakpoints {
  @include breakpoint($breakpoint) {
    // spacing resetters
    .u-#{$breakpoint}-p-0 {
      padding: 0;
    }

    .u-#{$breakpoint}-pt-0 {
      padding-top: 0;
    }

    &:not([dir='rtl']) .u-#{$breakpoint}-pr-0 {
      padding-right: 0;
    }

    .u-#{$breakpoint}-pb-0 {
      padding-bottom: 0;
    }

    &:not([dir='rtl']) .u-#{$breakpoint}-pl-0 {
      padding-left: 0;
    }

    .u-#{$breakpoint}-m-0 {
      margin: 0;
    }

    .u-#{$breakpoint}-mt-0 {
      margin-top: 0;
    }

    &:not([dir='rtl']) .u-#{$breakpoint}-mr-0 {
      margin-right: 0;
    }

    .u-#{$breakpoint}-mb-0 {
      margin-bottom: 0;
    }

    &:not([dir='rtl']) .u-#{$breakpoint}-ml-0 {
      margin-left: 0;
    }

    @each $size in map-keys($spacing-scale) {
      .u-#{$breakpoint}-p-#{$size} {
        padding: pad($size);
      }

      .u-#{$breakpoint}-pt-#{$size} {
        padding-top: pad($size);
      }

      &:not([dir='rtl']) .u-#{$breakpoint}-pr-#{$size} {
        padding-right: pad($size);
      }

      .u-#{$breakpoint}-pb-#{$size} {
        padding-bottom: pad($size);
      }

      &:not([dir='rtl']) .u-#{$breakpoint}-pl-#{$size} {
        padding-left: pad($size);
      }

      .u-#{$breakpoint}-m-#{$size} {
        margin: pad($size);
      }

      .u-#{$breakpoint}-mt-#{$size} {
        margin-top: pad($size);
      }

      &:not([dir='rtl']) .u-#{$breakpoint}-mr-#{$size} {
        margin-right: pad($size);
      }

      .u-#{$breakpoint}-mb-#{$size} {
        margin-bottom: pad($size);
      }

      &:not([dir='rtl']) .u-#{$breakpoint}-ml-#{$size} {
        margin-left: pad($size);
      }
    }
  }
}

// simple universal version
@each $size in map-keys($spacing-scale) {
  // spacing resetters
  .u-p-0 {
    @extend .u-s-p-0;
  }

  .u-pt-0 {
    @extend .u-s-pt-0;
  }

  .u-pr-0 {
    @extend .u-s-pr-0;
  }

  .u-pb-0 {
    @extend .u-s-pb-0;
  }

  .u-pl-0 {
    @extend .u-s-pl-0;
  }

  .u-m-0 {
    @extend .u-s-m-0;
  }

  .u-mt-0 {
    @extend .u-s-mt-0;
  }

  .u-mr-0 {
    @extend .u-s-mr-0;
  }

  .u-mb-0 {
    @extend .u-s-mb-0;
  }

  .u-ml-0 {
    @extend .u-s-ml-0;
  }

  .u-p-#{$size} {
    @extend .u-s-p-#{$size};
  }

  .u-pt-#{$size} {
    @extend .u-s-pt-#{$size};
  }

  .u-pr-#{$size} {
    @extend .u-s-pr-#{$size};
  }

  .u-pb-#{$size} {
    @extend .u-s-pb-#{$size};
  }

  .u-pl-#{$size} {
    @extend .u-s-pl-#{$size};
  }

  .u-m-#{$size} {
    @extend .u-s-m-#{$size};
  }

  .u-mt-#{$size} {
    @extend .u-s-mt-#{$size};
  }

  .u-mr-#{$size} {
    @extend .u-s-mr-#{$size};
  }

  .u-mb-#{$size} {
    @extend .u-s-mb-#{$size};
  }

  .u-ml-#{$size} {
    @extend .u-s-ml-#{$size};
  }
}
