@import '../settings/mixins/breakpoints';
@import '../settings/variables/spacing';

.ef-section {
  position: relative;
  width: 100%;
  @extend .-m;

  @each $size, $layoutMap in $layout-scale {
    &.-#{$size} {
      @each $item, $val in $layoutMap {
        @include breakpoint($item) {
          padding: layout($size, $item) 0;
        }
      }
    }
  }
}
