@import '../settings/variables/surface';

.ef-surface {
  padding: pad(m);
  background: white;

  *:last-child {
    margin-bottom: 0;
  }

  &.-rounded {
    border-radius: 4px;
  }

  &.-outline {
    border: 1px solid rgba(25, 25, 25, 0.2);
  }

  &.-shadow {
    box-shadow: var(
      --surface--shadow--box-shadow,
      $surface--shadow--box-shadow
    );
  }

  &.-deep-shadow {
    box-shadow: var(
      --surface--shadow--box-shadow-hover,
      $surface--shadow--box-shadow-hover
    );
  }

  @at-root a#{&},
    button#{&} {
    width: 100%;
    display: block;
    transition: var(--surface--hover-transition, $surface--hover-transition);
    cursor: pointer;

    &:hover {
      box-shadow: var(
        --surface--solid--box-shadow-hover,
        $surface--solid--box-shadow-hover
      );

      &.-outline {
        box-shadow: var(
          --surface--outline--box-shadow-hover,
          $surface--outline--box-shadow-hover
        );
      }
      &.-shadow {
        box-shadow: var(
          --surface--shadow--box-shadow-hover,
          $surface--shadow--box-shadow-hover
        );
      }
      &.-deep-shadow {
        box-shadow: var(
          --surface--deep-shadow--box-shadow-hover,
          $surface--deep-shadow--box-shadow-hover
        );
      }
    }
  }
}
