@import '../../settings/mixins/icons';
@import '../../settings/mixins/breakpoints';

html[dir='rtl'] {
  .ef-nav__item {
    @include breakpoint(m) {
      margin-right: 0;
      margin-left: 32px;
    }

    &.-is-active {
      .ef-nav__link {
        @include breakpoint(s only) {
          &:before {
            left: auto;
            right: 0;
          }
        }
      }
    }

    &.-with-sub-menu {
      .ef-nav__link {
        &:after {
          float: none;
          margin-left: 0px;
          margin-right: 5px;

          @include breakpoint(s only) {
            float: left;
          }
        }
      }
    }
  }

  .ef-nav__sub-menu {
    left: auto;
    right: -32px;
    padding: 14px 32px 14px 66px;

    @include breakpoint(s only) {
      padding: 0;
      margin: 0;
    }
  }

  .ef-nav__link {
    @include breakpoint(s only) {
      padding: 12px 32px 12px 42px;
    }
  }

  .ef-nav__sub-item {
    @include breakpoint(s only) {
      border-left: 0;
      border-right: 1px solid #efefef;
    }
  }

  .ef-nav__sub-link {
    @include breakpoint(s only) {
      padding: 12px 32px 12px 0;
    }
  }
}
