@mixin loadingSpinner() {
    position: relative;

    &::after {
        content: "";
        display: block;
        width: 1.2em;
        height: 1.2em;
        position: absolute;
        border: 2px solid;
        opacity: 0.3;
        left: calc(50% - 0.6em);
        top: calc(50% - 0.6em);
        border-color: transparent transparent currentColor currentColor;
        border-radius: 50%;
        transform-origin: 50% 50%;
        animation: ef-loading-spinner 0.4s linear infinite;
    }
}
