@import './breakpoints';
@import '../variables/grid';
@import '../utils/map';

@mixin container() {
  @each $breakpoint, $size in $breakpoints {
    @include breakpoint($breakpoint only) {
      max-width: map-deep-get(
        $container-settings,
        '#{$breakpoint}',
        'maxWidth'
      );
      margin: 0 map-deep-get($container-settings, '#{$breakpoint}', 'marginX');
    }
  }
}

@mixin row($prefix: '') {
  &.#{$prefix}-reverse {
    flex-direction: row-reverse;
  }

  /**
    * Horizontal alignment -> justify content
     */
  &.#{$prefix}-x-start {
    justify-content: flex-start;
  }

  &.#{$prefix}-x-end {
    justify-content: flex-end;
  }

  &.#{$prefix}-x-center {
    justify-content: center;
  }

  &.#{$prefix}-x-around {
    justify-content: space-around;
  }

  &.#{$prefix}-x-between {
    justify-content: space-between;
  }

  /**
    * Vertical alignment -> align items
    **/
  &.#{$prefix}-y-start {
    align-items: flex-start;
  }

  &.#{$prefix}-y-center {
    align-items: center;
  }

  &.#{$prefix}-y-end {
    align-items: flex-end;
  }

  &.#{$prefix}-y-stretch {
    align-items: stretch;
  }

  &.#{$prefix}-no-gutters {
    margin-left: 0;
    margin-right: 0;
  }
}

@mixin col-common() {
  position: relative;
  max-width: 100%;

  // split @include flex(0, 0, auto) into individual props
  flex-basis: 0;
  flex-grow: 1;
  flex-shrink: 0;
}

@mixin col($prefix: '') {
  @for $i from 1 through $grid-columns {
    &.#{$prefix}-#{$i} {
      //@include e-col-common;
      max-width: 100% / $grid-columns * $i;
      flex-basis: 100% / $grid-columns * $i;
    }
  }

  @for $i from 1 through $grid-columns {
    &.#{$prefix}-offset-#{$i} {
      //@include e-col-common;
      margin-left: 100% / $grid-columns * $i;
    }
  }

  &.#{$prefix}-y-start {
    align-self: flex-start;
  }

  &.#{$prefix}-y-center {
    align-self: center;
  }

  &.#{$prefix}-y-end {
    align-self: flex-end;
  }

  &.#{$prefix}-x-start {
    margin-right: auto;
    margin-left: unset;
  }

  &.#{$prefix}-x-center {
    margin-right: auto;
    margin-left: auto;
  }

  &.#{$prefix}-x-end {
    margin-left: auto;
    margin-right: unset;
  }

  &.#{$prefix}-first {
    order: -1;
  }

  &.#{$prefix}-last {
    order: 1;
  }

  &.#{$prefix}-shrink {
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 1;
  }

  &.#{$prefix}-grow {
    flex-basis: auto;
    flex-grow: 1;
    flex-shrink: 0;
  }

  &.#{$prefix}-collapse {
    padding-left: 0;
    padding-right: 0;
  }
}
