@import '../settings/mixins/breakpoints';
@import '../settings/variables/color';

.ef-list {
  margin: 0 0 24px;
  padding-left: 16px;
  color: var(--color-text, $color-text);

  &.-small {
    font-size: 14px;
    line-height: 22px;

    @include breakpoint(m) {
      font-size: 12px;
    }
  }

  &.-flat {
    list-style: none;
    padding-left: 0;
  }
}

.ef-list__item {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }
}
