@import '../settings/variables/surface';
@import '../settings/variables/color';
@import '../settings/variables/general';
@import '../settings/variables/surface';
@import '../settings/mixins/common';
@import '../settings/mixins/scrim-gradient';
@import '../settings/mixins/breakpoints';
@import '../typography/variables/font-weights';

$c-card--teaser--sm--content-margin: 24px;
$c-card--teaser--md--content-margin: 32px;

$c-card--content--sm--content-margin: 32px;
$c-card--content--md--content-margin: 32px;

$c-card--content-skeleton--shapes-bg: #fff;
$c-card--content-skeleton--bg: #d7d7d7;
$c-card--content-skeleton-sm--image-height: 200px;
$c-card--content-skeleton--title-width: 150px;
$c-card--content-skeleton--title-height: 36px;

$c-card--content-skeleton-sm--title-position-x: $c-card--content--sm--content-margin;
$c-card--content-skeleton-sm--title-position-y: 220px;

$c-card--content-skeleton-md--title-position-x: calc(
  50% + #{$c-card--content-skeleton--title-width / 2 +
    $c-card--content--md--content-margin}
);
$c-card--content-skeleton-md--title-position-y: 32px;

$c-card--content-skeleton--text-width: 190px;
$c-card--content-skeleton--text-height: 18px;
$c-card--content-skeleton-sm--text-position-x: $c-card--content--sm--content-margin;
$c-card--content-skeleton-sm--text-position-y: 280px;

$c-card--content-skeleton-md--text-position-x: calc(
  50% + #{$c-card--content-skeleton--text-width / 2 +
    $c-card--content--md--content-margin}
);
$c-card--content-skeleton-md--text-position-y: 86px;

:root {
  --c-card--teaser--sm--content-margin: #{$c-card--teaser--sm--content-margin};
  --c-card--teaser--md--content-margin: #{$c-card--teaser--md--content-margin};

  --c-card--content--sm--content-margin: #{$c-card--content--sm--content-margin};
  --c-card--content--md--content-margin: #{$c-card--content--md--content-margin};

  --c-card--content-skeleton--shapes-bg: #{$c-card--content-skeleton--shapes-bg};
  --c-card--content-skeleton--bg: #{$c-card--content-skeleton--bg};
  --c-card--content-skeleton-sm--image-height: #{$c-card--content-skeleton-sm--image-height};
  --c-card--content-skeleton--title-width: #{$c-card--content-skeleton--title-width};
  --c-card--content-skeleton--title-height: #{$c-card--content-skeleton--title-height};

  --c-card--content-skeleton-sm--title-position-x: #{$c-card--content-skeleton-sm--title-position-x};
  --c-card--content-skeleton-sm--title-position-y: #{$c-card--content-skeleton-sm--title-position-y};

  --c-card--content-skeleton-md--title-position-x: #{$c-card--content-skeleton-md--title-position-x};
  --c-card--content-skeleton-md--title-position-y: #{$c-card--content-skeleton-md--title-position-y};

  --c-card--content-skeleton--text-width: #{$c-card--content-skeleton--text-width};
  --c-card--content-skeleton--text-height: #{$c-card--content-skeleton--text-height};
  --c-card--content-skeleton-sm--text-position-x: #{$c-card--content-skeleton-sm--text-position-x};
  --c-card--content-skeleton-sm--text-position-y: #{$c-card--content-skeleton-sm--text-position-y};

  --c-card--content-skeleton-md--text-position-x: #{$c-card--content-skeleton-md--text-position-x};
  --c-card--content-skeleton-md--text-position-y: #{$c-card--content-skeleton-md--text-position-y};
}

// Teaser card

.ef-card--teaser {
  display: block;
  border-radius: 4px;
  box-shadow: var(
    --surface--shadow--box-shadow-hover,
    $surface--shadow--box-shadow-hover
  );
  transition: var(--surface--hover-transition, $surface--hover-transition);
  @include setRatio(4/3);

  &:after {
    content: '';
    position: absolute;
    right: -1px;
    bottom: -1px;
    left: -1px;
    height: 50%;
    display: block;
    opacity: 0.7;

    @include scrim-gradient($color-ink-black, 'to top');
  }

  &:hover {
    box-shadow: var(
      --surface--deep-shadow--box-shadow-hover,
      $surface--deep-shadow--box-shadow-hover
    );
  }

  @include breakpoint(m) {
    @include setRatio(3/2);
  }

  &.-vertical {
    @include setRatio(304/530);
    text-align: center;

    .ef-card--teaser__title {
      font-size: 28px;
    }
  }

  &.-small {
    @include setRatio(1);

    @include breakpoint(m) {
      @include setRatio(3/2);
    }

    .ef-card--teaser__title {
      margin-bottom: 0;
    }
  }

  &.-gradient-light:after {
    opacity: 0.1;
  }

  &.-gradient-midlight:after {
    opacity: 0.3;
  }

  &.-gradient-mid:after {
    opacity: 0.5;
  }

  &.-gradient-dark:after {
    opacity: 1;
  }
}

.ef-card--teaser__img {
  @include setFluid(0);
}

.ef-card--teaser__inner {
  position: absolute;
  right: var(
    --c-card--teaser--sm--content-margin,
    $c-card--teaser--sm--content-margin
  );
  bottom: 0;
  left: var(
    --c-card--teaser--sm--content-margin,
    $c-card--teaser--sm--content-margin
  );
  z-index: 1;

  @include breakpoint(m) {
    right: var(
      --c-card--teaser--md--content-margin,
      $c-card--teaser--md--content-margin
    );
    left: var(
      --c-card--teaser--md--content-margin,
      $c-card--teaser--md--content-margin
    );
  }
}

.ef-card--teaser__content {
  margin-bottom: var(
    --c-card--teaser--sm--content-margin,
    $c-card--teaser--sm--content-margin
  );
  color: var(--color-white, $color-white);

  @include breakpoint(m) {
    margin-bottom: var(
      --c-card--teaser--md--content-margin,
      $c-card--teaser--md--content-margin
    );
  }
}

.ef-card--teaser__title {
  margin-bottom: 4px;
  color: inherit;
}

.ef-card--teaser__text {
  &,
  & > * {
    color: currentColor;
    font-size: 14px;
    font-weight: normal;
  }
}

// Content card

.ef-card--content {
  position: relative;
  overflow: hidden;
  display: block;
  border-radius: 4px;
  background: var(--color-white, $color-white);
  box-shadow: var(--surface--shadow--box-shadow, $surface--shadow--box-shadow);

  @if ($includeLoaders) {
    // we use pseudo-elements for loading state
    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 1;
      display: block;
      opacity: 0;
    }

    &:before {
      visibility: hidden;
      opacity: 0;
      transition: opacity 0.2s, visibility 0s 0.2s;

      @include breakpoint(s only) {
        // skeleton
        $shapes: ();
        // title
        $shapes: append(
          $shapes,
          rectangle(
            var(
              --c-card--content-skeleton--shapes-bg,
              $c-card--content-skeleton--shapes-bg
            ),
            var(
              --c-card--content-skeleton--title-width,
              $c-card--content-skeleton--title-width
            ),
            var(
              --c-card--content-skeleton--title-height,
              $c-card--content-skeleton--title-height
            ),
            var(
              --c-card--content-skeleton-sm--title-position-x,
              $c-card--content-skeleton-sm--title-position-x
            ),
            var(
              --c-card--content-skeleton-sm--title-position-y,
              $c-card--content-skeleton-sm--title-position-y
            )
          )
        );
        // text
        $shapes: join(
          $shapes,
          lines(
            4,
            var(
              --c-card--content-skeleton--shapes-bg,
              $c-card--content-skeleton--shapes-bg
            ),
            var(
              --c-card--content-skeleton--text-width,
              $c-card--content-skeleton--text-width
            ),
            var(
              --c-card--content-skeleton--text-height,
              $c-card--content-skeleton--text-height
            ),
            10px,
            var(
              --c-card--content-skeleton-sm--text-position-x,
              $c-card--content-skeleton-sm--text-position-x
            ),
            var(
              --c-card--content-skeleton-sm--text-position-y,
              $c-card--content-skeleton-sm--text-position-y
            ),
            140px
          )
        );
        // Image
        $shapes: append(
          $shapes,
          rectangle(
            var(
              --c-card--content-skeleton--shapes-bg,
              $c-card--content-skeleton--shapes-bg
            ),
            100%,
            var(
              --c-card--content-skeleton-sm--image-height,
              $c-card--content-skeleton-sm--image-height
            ),
            0,
            0
          )
        );
        // Background
        $shapes: append(
          $shapes,
          rectangle(
            var(--c-card--content-skeleton--bg, $c-card--content-skeleton--bg),
            100%,
            100%,
            0,
            0
          )
        );
        @include skeleton($shapes...);
      }

      @include breakpoint(m) {
        // skeleton
        $shapes: ();
        // title
        $shapes: append(
          $shapes,
          rectangle(
            var(
              --c-card--content-skeleton--shapes-bg,
              $c-card--content-skeleton--shapes-bg
            ),
            var(
              --c-card--content-skeleton--title-width,
              $c-card--content-skeleton--title-width
            ),
            var(
              --c-card--content-skeleton--title-height,
              $c-card--content-skeleton--title-height
            ),
            var(
              --c-card--content-skeleton-md--title-position-x,
              $c-card--content-skeleton-md--title-position-x
            ),
            var(
              --c-card--content-skeleton-md--title-position-y,
              $c-card--content-skeleton-md--title-position-y
            )
          )
        );
        // text
        $shapes: join(
          $shapes,
          lines(
            4,
            var(
              --c-card--content-skeleton--shapes-bg,
              $c-card--content-skeleton--shapes-bg
            ),
            var(
              --c-card--content-skeleton--text-width,
              $c-card--content-skeleton--text-width
            ),
            var(
              --c-card--content-skeleton--text-height,
              $c-card--content-skeleton--text-height
            ),
            10px,
            var(
              --c-card--content-skeleton-md--text-position-x,
              $c-card--content-skeleton-md--text-position-x
            ),
            var(
              --c-card--content-skeleton-md--text-position-y,
              $c-card--content-skeleton-md--text-position-y
            ),
            140px,
            calc(
              50% + #{140px / 2 +
                var(
                  --c-card--content--sm--content-margin,
                  $c-card--content--sm--content-margin
                )}
            )
          )
        );
        // Image
        $shapes: append(
          $shapes,
          rectangle(
            var(
              --c-card--content-skeleton--shapes-bg,
              $c-card--content-skeleton--shapes-bg
            ),
            50%,
            100%,
            0,
            0
          )
        );
        // Background
        $shapes: append(
          $shapes,
          rectangle(
            var(--c-card--content-skeleton--bg, $c-card--content-skeleton--bg),
            100%,
            100%,
            0,
            0
          )
        );
        @include skeleton($shapes...);
      }
    }

    &:after {
      top: 200px;
      left: 0;
      width: 30%;
      height: 100%;
      z-index: 1;
      display: block;
      visibility: hidden;

      background: linear-gradient(
        90deg,
        rgba(
          var(--c-card--content-skeleton--bg, $c-card--content-skeleton--bg),
          0
        ),
        rgba(
          var(--c-card--content-skeleton--bg, $c-card--content-skeleton--bg),
          0.3
        ),
        rgba(
          var(--c-card--content-skeleton--bg, $c-card--content-skeleton--bg),
          0
        )
      );
      animation-name: ef-skeleton-loading;
      animation-fill-mode: forwards;
      animation-duration: 1.5s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;

      @include breakpoint(m) {
        top: 0;
        left: 50%;
        width: 20%;
      }
    }
  }

  @include breakpoint(m) {
    min-height: 312px;
    display: flex;
  }

  &.-vertical {
    display: block;

    .ef-card--content__img {
      width: auto;
      @include setRatio(3/2);
    }

    .ef-card--content__inner {
      width: auto;
    }
  }

  &.-outline {
    box-shadow: inset 0 0 0 1px
      var(--c-card--outline-color, $c-card--outline-color);
  }

  &.-no-shadow {
    box-shadow: none;
  }

  @if ($includeLoaders) {
    &.-is-loading {
      &:before {
        visibility: visible;
        opacity: 1;
        transition: opacity 0.2s, visibility 0s;
      }
      &:after {
        visibility: visible;
      }
      @include breakpoint(s only) {
        min-height: 400px;
      }
    }
  }
}

a.ef-card--content {
  transition: box-shadow 0.2s ease-out;
  text-decoration: none;
  box-shadow: var(--surface--shadow--box-shadow, $surface--shadow--box-shadow);

  &:hover,
  &:active,
  &:focus {
    text-decoration: none;
  }

  &:hover {
    box-shadow: var(
      --surface--shadow--box-shadow-hover,
      $surface--shadow--box-shadow-hover
    );
  }
  &:active {
    transition: box-shadow 0.1s ease-out;
    box-shadow: var(
      --surface--shadow--box-shadow,
      $surface--shadow--box-shadow
    );
  }
}

.ef-card--content__img {
  display: block;
  background-size: cover;
  background-position: center;

  @include breakpoint(s only) {
    @include setRatio(327/212);
  }

  @include breakpoint(m) {
    width: 50%;
  }
}

.ef-card--content__inner {
  display: flex;
  flex-direction: column;
  padding: var(
    --c-card--content--sm--content-margin,
    $c-card--content--sm--content-margin
  );

  @include breakpoint(m) {
    width: 50%;
    padding: var(
      --c-card--content--md--content-margin,
      $c-card--content--md--content-margin
    );
  }
}

.ef-card--content__content {
  --color-text: #{$color-text};
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.ef-card--content__title {
  margin-bottom: 8px;
}

.ef-card--content__text {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  line-height: 1.5;

  p {
    font-weight: normal;

    &:last-of-type {
      margin-bottom: 16px;
    }
  }
}

.ef-card--content__link {
  margin-top: auto;
  font-weight: normal;
  font-weight: var(--fw-bold, $fw-bold);
  color: var(--color-text, $color-text);
  font-size: 14px;
  line-height: 1.7142857143;
}

// Video card

.ef-card--video {
  @include setRatio(16/9);

  display: block;
  border-radius: 4px;
  transition: var(--surface--hover-transition, $surface--hover-transition);
  box-shadow: var(--surface--shadow--box-shadow, $surface--shadow--box-shadow);

  &:before,
  &:after {
    display: block;
  }

  &:hover {
    box-shadow: var(
      --surface--shadow--box-shadow-hover,
      $surface--shadow--box-shadow-hover
    );
  }
}

.ef-card--video__img {
  @include setFluid();
  background-size: cover;
  background-position: center;
}

.ef-card--video__icon-w {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 72px;
  height: 72px;
  z-index: 1;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: var(--color-white, $color-white);
}

.modal__player-video {
  position: absolute;
  top: 0;
  left: 0;
  &-wrap {
    position: relative;
    padding-top: 56.25%;
  }
}
