$root-unit: 8 !default;

$spacing-scale: (
    "xxs": $root-unit / 2,
    // 4
        "xs": $root-unit,
    // 8
        "s": $root-unit * 2,
    // 16
        "m": $root-unit * 3,
    // 24
        "l": $root-unit * 4,
    // 32
        "xl": $root-unit * 5,
    // 40
        "xxl": $root-unit * 8,
    // 64
) !default;

@function pad($size: m) {
    @if map_has_key($spacing-scale, $size) {
        @return map_get($spacing-scale, $size) + px;
    } @else {
        @warn 'Key #{$size} doesn\'t exist for map $spacing-scale';
    }
}

$layout-unit: 32 !default;
// Used to define section spacings per breakpoint
$layout-scale: (
    s: (
        s: $layout-unit,
        m: $layout-unit * 1.5,
        l: $layout-unit * 2,
        xl: $layout-unit * 3
    ),
    m: (
        s: $layout-unit * 1.5,
        m: $layout-unit * 2,
        l: $layout-unit * 3,
        xl: $layout-unit * 4
    ),
    l: (
        s: $layout-unit * 2,
        m: $layout-unit * 3,
        l: $layout-unit * 4,
        xl: $layout-unit * 5
    ),
    xl: (
        s: $layout-unit * 3,
        m: $layout-unit * 4,
        l: $layout-unit * 5,
        xl: $layout-unit * 6
    )
) !default;

@function layout($sizeMap: m, $breakpoint: m) {
    @if map_has_key($layout-scale, $sizeMap) {
        @return map_deep_get($layout-scale, $sizeMap, $breakpoint) + px;
    } @else {
        @warn 'Key doesn\'t exist for map $layout-scale';
    }
}
