@import '../typography/variables/font-weights';
@import '../settings/variables/buttons';

.ef-tag {
    height: 32px;
    padding: 8px 12px;
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    line-height: 1.143;
    border-radius: 4px;
    background-clip: padding-box;
    font-weight: $fw-book;

  &.-label {
    color: var(--color-text);
    box-shadow: inset 0 0 0 1px
      rgba(var(--color-border-interactive, $color-border-interactive), 0.3);

    .-inv &,
    .-has-dark-bg &,
    &.-on-dark{
        box-shadow: inset 0 0 0 1px
        rgba(
                var(
                        --color-border-interactive--light,
                        $color-border-interactive--light
                ),
                0.2
        );
        }

        &.-small {
            height: 20px;
            padding: 0 4px;
            border-radius: 2px;
            line-height: 20px;
            font-size: 12px;
        }

    &.-filled {
      color: $color-white;
      background-color: var(--color-primary, $color-primary);
      box-shadow: none;
    }
  }

    &.-button {
        cursor: pointer;
        transition: all 0.15s ease-out;

        &:not(.-is-selected) {
            color: $btn-filled-color;
            background: $btn-filled-bg;
            box-shadow:  0 2px 8px 0 rgba($btn-filled-shadow-color, 0.2);
            color: var(--btn-tag-color, var(--btn-filled-color, $btn-filled-color));
            background: var(--btn-tag-bg, var(--btn-filled-bg, $btn-filled-bg));
            box-shadow: var(--btn-tag-box-shadow, 0 2px 8px 0 rgba($btn-filled-shadow-color, 0.2));

            &:hover,
            &.-hover {
                box-shadow: 0 3px 8px 0 rgba($btn-filled-shadow-color, 0.24);
                box-shadow: var(--btn-tag-box-shadow--hover, 0 3px 8px 0 rgba($btn-filled-shadow-color, 0.24));

                .-inv &,
                .-has-dark-bg &,
                &.-on-dark {
                    box-shadow: 0 0 0 0 rgba($btn-filled-shadow-color, 0);

                    &.-focus-keyboard {
                        box-shadow: var(--btn-focus-ring--color, none);
                    }
                }
            }

            &:disabled,
            &[disabled],
            &.-is-disabled {
                color: $btn-filled-color--disabled;
                box-shadow: 0 2px 4px rgba($btn-filled-shadow-color, 0.1);
                color: var(--btn-tag-color--disabled, $btn-filled-color--disabled);
                box-shadow: var(--btn-tag-box-shadow--disabled, 0 2px 4px rgba($btn-filled-shadow-color, 0.1));
            }
        }

        &.-is-selected {
            color: $btn-primary-color;
            background: $btn-primary-bg;
            box-shadow: 0 2px 8px 0 $btn-primary-shadow-color;
            color: var(--btn-tag-selected-color, $btn-primary-color);
            background: var(--btn-tag-selected-bg, $btn-primary-bg);
            box-shadow: var(--btn-tag-selected-box-shadow, 0 2px 8px 0 $btn-primary-shadow-color);

            &.-focus-keyboard {
                box-shadow: var(--btn-focus-ring--color, none);
            }
        }
    }
}

.ef-tag__icon {
    margin-right: pad(xs);
}
