@import '../../settings/mixins/icons';

// reverse relevant icons

html[dir='rtl'] {
  .ef-icon {
    &.-chevron-left {
      @include iconBG(chevron-right);
    }

    &.-chevron-right {
      @include iconBG(chevron-left);
    }

    &.-chevron-double-left {
      @include iconBG(chevron-double-right);
    }

    &.-chevron-double-right {
      @include iconBG(chevron-double-left);
    }
  }
}
