@import '../variables/general';
@import './breakpoints';

@mixin clearfix() {
  &::before,
  &::after {
    content: ' ';
    display: table;
  }

  &::after {
    clear: both;
  }
}

//element that takes full width/height of containing relative parent
@mixin setFluid($zIndex: 1) {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $zIndex;
}

//sets ratio to element, cascade parameter makes child images fluid
@mixin setRatio($ratio: 4/3, $cascade: false) {
  position: relative;
  height: 0;
  overflow: hidden;
  padding-bottom: 100 / $ratio * 1%;

  @if ($cascade) {
    > img {
      @include setFluid();
    }
  }
}

//parses a propriety map and generates it as as css properties
@mixin parseProps($propList) {
  @each $attr, $prop in $propList {
    #{$attr}: #{$prop};
  }
}

//parses a state nested states list and renders each state's css
@mixin parseState($stateList) {
  @each $state, $properties in $stateList {
    @if ($state == base) {
      @include parseProps($properties);
    } @else {
      &:#{$state} {
        @include parseProps($properties);
      }
    }
  }
}

// ellipsis helper
@mixin ellipsis() {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

// Focus shadow
@mixin inputFocusShadow($color: $color-ui-accent) {
  box-shadow: 0 4px 12px 0 rgba($color, 0.5);
}

// Prevent inputs getting too wide at small breakpoint
@mixin inputMaxWidth($width: $inputMaxWidth) {
  @include breakpoint(s only) {
    max-width: $width + px;
  }
}
