@import '../typography/variables/font-weights';
@import '../settings/variables/color';
@import '../settings/mixins/common';
// Labels
.ef-form-label {
  display: inline-block;
  color: var(--color-text, $color-text);
  font-weight: var(--fw-book, $fw-book);
  font-size: 14px;
  margin-bottom: pad(xs);
}

.ef-form-label__required[title] {
  color: var(--color-notification-warning, $color-notification-warning);
  text-decoration: none;
}

// Groups
.ef-form-group {
  display: flex;
  flex-direction: column;

  &.-inline {
    flex-wrap: wrap;
    flex-direction: row;
    margin-left: -#{pad(m)};
    > * {
      margin-left: pad(m);
    }
  }
}

.ef-form-group,
.ef-row,
.ef-input-w {
  & + .ef-form-validation {
    margin-top: pad(xs);
  }
}

// Set max width at s breakpoint
.ef-input-w,
.ef-form-fieldset,
.ef-form-label {
  @include inputMaxWidth();
}

// Fieldsets
.ef-form-fieldset {
  .ef-input-w {
    margin-top: pad(s);
  }

  .ef-form-label {
    margin-bottom: 0;

    & + .ef-input-w {
      margin-top: pad(xs);
    }
    & + .ef-row,
    & + .ef-form-group {
      margin-top: -#{pad(xs)};
    }
  }
}

// Validation
.ef-form-validation {
  display: none;
  font-weight: var(--fw-book, $fw-book);
  font-size: 12px;
  line-height: 20px;

  &.-is-valid {
    color: var(--color-notification-success, $color-notification-success);
  }

  &.-is-invalid {
    color: var(--color-notification-warning, $color-notification-warning);
  }
}

.ef-form-fieldset,
.ef-form-group,
.ef-validate {
  &.-is-valid {
    .ef-form-validation.-is-valid {
      display: block;
    }
  }
  &.-is-invalid {
    .ef-form-validation.-is-invalid {
      display: block;
    }
  }
}

.ef-form-validation {
  padding-left: pad(s);
}

.ef-form-group.-inline {
  & + .ef-form-validation {
    padding-left: 0;
  }
}

/* start floating label*/

label.floating-label {
  display: inline-block;
  position: absolute;
  left: 17px;
  top: 12px;
  color: currentColor;
  opacity: 0.5;
  pointer-events: none;
  cursor: text;
  transition: all 150ms ease-in;

  &.field-active {
    color: #979797; // this is the result of currentColor with .5 opacity
    transform: translateY(-25px);
    font-size: 90%;
    line-height: 0.9;
    opacity: 1;
    text-shadow: 1px 0 0 var(--input-bg), -1px 0 0 var(--input-bg),
      2px 0 0 var(--input-bg), -2px 0 0 var(--input-bg), 0 1px 0 var(--input-bg),
      0 -1px 0 var(--input-bg), 0 2px 0 var(--input-bg),
      0 -2px 0 var(--input-bg);
    background-color: var(--input-bg);
    padding: 0 4px;
    top: 18px;
    border-radius: 4px;
  }
}

.input-floating-label {
  -webkit-appearance: none !important;
}

/* end floating label*/
