@import '../settings/mixins/breakpoints';
@import '../settings/mixins/resets';
@import '../settings/variables/color';
@import '~swiper/swiper';
@import '~swiper/components/effect-fade/effect-fade';
@import '~swiper/components/pagination/pagination';
@import '~swiper/components/navigation/navigation';
@import '~swiper/components/scrollbar/scrollbar';

$c-carousel--pagination-distance-sm: 36px;
$c-carousel--pagination-distance-md: 54px;
$c-carousel--offsetX-sm: containerMarginX(s);
$c-carousel__btn-icon--size: 52px;

:root {
  --c-carousel--pagination-distance-sm: #{$c-carousel--pagination-distance-sm};
  --c-carousel--pagination-distance-md: #{$c-carousel--pagination-distance-md};
  --c-carousel--offsetX-sm: #{$c-carousel--offsetX-sm};
  --c-carousel__btn-icon--size: #{$c-carousel__btn-icon--size};
}

.ef-carousel {
  position: relative;
  border-bottom: transparent
    var(
      --c-carousel--pagination-distance-sm,
      $c-carousel--pagination-distance-sm
    )
    solid;

  @include breakpoint(m) {
    margin: 0 -28px;
    padding-left: 28px;
    padding-right: 28px;
    border-bottom: transparent
      var(
        --c-carousel--pagination-distance-md,
        $c-carousel--pagination-distance-md
      )
      solid;
  }
}

.ef-carousel__inner {
  overflow: hidden;

  @include breakpoint(s only) {
    margin: -10px (-var(--c-carousel--offsetX-sm, $c-carousel--offsetX-sm));
    padding: 10px var(--c-carousel--offsetX-sm, $c-carousel--offsetX-sm);
  }

  @include breakpoint(m) {
    // gap for card shadows...
    margin: -10px;
    padding: 10px;
  }
}

.ef-carousel__carousel.swiper-container {
  overflow: visible;
}

.ef-carousel__btn {
  @include reset-button;
  position: absolute;
  top: 0;
  width: 10%;
  height: 100%;
  z-index: 1;
  cursor: pointer;
  transition: opacity 0.12s;

  @include breakpoint(s only) {
    display: none;
  }

  &.swiper-button-disabled {
    opacity: 0;
  }
}

.ef-carousel__btn--prev {
  left: calc(
    var(--c-carousel__btn-icon--size, $c-carousel__btn-icon--size) / 2
  );
}

.ef-carousel__btn--next {
  right: calc(
    var(--c-carousel__btn-icon--size, $c-carousel__btn-icon--size) / 2
  );
  text-align: right;
}

.ef-carousel__btn-icon {
  position: absolute;
  top: 50%;
  width: var(--c-carousel__btn-icon--size, $c-carousel__btn-icon--size);
  height: var(--c-carousel__btn-icon--size, $c-carousel__btn-icon--size);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -var(--c-carousel__btn-icon--size, $c-carousel__btn-icon--size) /
    2;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 2px 15px 0 rgba(25, 25, 25, 0.16);
}

.ef-carousel__btn-icon--prev {
  left: -26px;
}

.ef-carousel__btn-icon--next {
  right: -26px;
}

// pagination

.ef-carousel__pagination {
  width: 100%;
  height: 24px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;

  @include breakpoint(m) {
    justify-content: center;
  }

  &.-start {
    justify-content: flex-start;
  }

  &.-center {
    justify-content: center;
  }

  &.-end {
    justify-content: flex-end;
  }

  .swiper-pagination-bullet {
    width: 4px;
    height: 4px;
    margin: 0 5px;
    flex: 0 0 auto;
    border-radius: 2px;
    transition: width 0.2s ease;
    opacity: 1;
    background: var(--color-text, $color-text);

    &.swiper-pagination-bullet-active {
      width: 18px;
    }
  }
}
