@import '../settings/variables/_spacing.scss';

.ef-accordion__item {
  border-bottom: 1px solid var(--color-ui-border);

  &.-inline {
    border: none;
  }
}

.ef-accordion__title {
  width: 100%;
  max-width: none;
  margin: 0;
  padding: pad(s) 0;
  display: block;
  text-align: left;
  user-select: none;
  cursor: pointer;
}

.ef-accordion__title--inline {
  display: inline-flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
}

.ef-accordion__title,
.ef-accordion__title--inline {
  > *:last-child {
    margin-bottom: 0;
  }
}

.ef-accordion__content {
  p:last-child,
  .ef-text-para:last-child,
  .ef-text-body:last-child {
    padding-bottom: pad(l);
  }
}
