@import "./color";

$box-shadow-dark-enabled: 0 2px 8px 0 rgba($color-ink-black, 0.2) !default;
$box-shadow-dark-hover: 0 3px 8px 0 rgba($color-ink-black, 0.24) !default;
$box-shadow-dark-disabled: 0 2px 4px rgba($color-ink-black, 0.1) !default;

// Primary buttons
$btn-primary-shadow-color: $color-primary !default;

$btn-primary-color: white !default;
$btn-primary-bg: linear-gradient(#06B4EA, $color-primary) !default;

// Outlined buttons
$btn-outlined-shadow-color: $color-ink-black;

$btn-outlined-bg: transparent !default;
$btn-outlined-border-color: rgba($color-ink-black, 0.2) !default;
$btn-outlined-color: $color-text !default;

$btn-outlined-border-color--hover: rgba($color-ink-black, 0.13) !default;

$btn-outlined-border-color--disabled: rgba($color-ink-black, 0.2) !default;
$btn-outlined-color--disabled: rgba($btn-outlined-color, 0.6) !default;

$btn-outlined-on-dark-border-color: rgba($color-white, 0.3) !default;
$btn-outlined-on-dark-color: $color-text--light !default;

$btn-outlined-on-dark-bg--hover: $color-white !default;
$btn-outlined-on-dark-color--hover: $color-text !default;

$btn-outlined-on-dark-border-color--disabled: rgba($color-white, 0.13) !default;
$btn-outlined-on-dark-color--disabled: rgba($btn-outlined-on-dark-border-color, 0.3) !default;

// Filled Button
$btn-filled-shadow-color: $color-ink-black;

$btn-filled-bg: $color-white !default;
$btn-filled-color: $color-text !default;

$btn-filled-color--hover: $btn-filled-color !default;
$btn-filled-color--disabled: rgba($btn-filled-color, 0.6) !default;

$btn-filled-on-dark-color--hover: $btn-primary-color !default;
$btn-filled-on-dark-bg--hover: $btn-primary-bg !default;
$btn-filled-on-dark-shadow-color--hover: $btn-primary-shadow-color !default;

$btn-filled-on-dark-color--disabled: $btn-primary-color !default;
$btn-filled-on-dark-bg--hover: $btn-primary-bg !default;
$btn-filled-on-dark-shadow-color--hover: $btn-primary-shadow-color !default;

// Keyboard focus
$btn-focus-ring--color: 0 0 0 0.25em rgba($btn-primary-shadow-color, 0.3) !default;
$btn-focus-ring--mono-on-light: 0 0 0 0.25em rgba($color-ink-black, 0.3) !default;
$btn-focus-ring--mono-on-dark: 0 0 0 0.25em rgba($color-white, 0.3) !default;
