@import '../settings/mixins/breakpoints';

.ef-modal__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  overflow: auto;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(black, 0.3);
  opacity: 0;
  transition: opacity 0.3s ease-out;

  &.-lightbox {
    padding: 52px 8px;
    background-color: rgba(black, 0.8);
  }

  &.ef-modal__overlay--after-open {
    opacity: 1;
  }

  &.ef-modal__overlay--before-close {
    opacity: 0;
  }
}

.ef-modal {
  position: relative;
  width: calc(100% - 16px);
  margin: 32px 0 0;
  padding: 0;
  transform: translateY(40px);
  opacity: 0;
  transition: opacity 0.2s 0.1s ease-out, transform 0.3s ease-out;

  @include breakpoint(m) {
    margin: auto 0;
  }

  &:focus {
    outline: none;
  }

  .ef-modal__overlay--after-open & {
    transform: translateY(0);
    opacity: 1;
  }

  .ef-modal__overlay.-lightbox & {
    max-height: calc(100vh - 116px);
  }

  &.-s {
    max-width: 600px;
  }

  &.-m {
    max-width: 960px;
  }

  &.-l {
    max-width: 1200px;
  }

  &.-xl {
    max-width: 1600px;
  }

  &.-top {
    align-self: flex-start;
  }

  &.-bottom {
    align-self: flex-start;
  }
}

.ef-modal__content {
  padding: pad(xxl) pad(l);

  @include breakpoint(m) {
    padding: pad(xxl);
  }
}

.ef-modal__dismiss {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 40px;
  height: 40px;
  cursor: pointer;

  @include breakpoint(m) {
    top: 20px;
    right: 20px;
  }

  .ef-modal__overlay.-lightbox & {
    top: -44px;
    right: -12px;
  }
}
