///
/// utils
///
@import '../typography/variables/stacks';
@import '../typography/variables/font-weights';

.u-f-circular {
  font-family: $latin-stack;
}

.u-f-light {
  font-weight: var(--fw-light, $fw-light)
}

.u-f-book {
  font-weight: var(--fw-book, $fw-book);
}

.u-f-medium {
  font-weight: var(--fw-medium, $fw-medium);
}

.u-f-bold {
  font-weight: var(--fw-bold, $fw-bold);
}

.u-f-black {
  font-weight: var(--fw-black, $fw-black);
}

.u-text-right {
  text-align: right;
}

.u-text-upper {
  text-transform: uppercase;
}

@each $breakpoint, $bpSize in $breakpoints {
  @include breakpoint($breakpoint) {
    .u-#{$breakpoint}-text-left {
      text-align: left;
    }
    .u-#{$breakpoint}-text-center {
      text-align: center;
    }
    .u-#{$breakpoint}-text-right {
      text-align: right;
    }
  }
}

.u-text-left {
  text-align: left;
}

.u-text-center {
  text-align: center;
}

.u-text-right {
  text-align: right;
}

// position
.u-xy-abs-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  backface-visibility: hidden;
}
