html[dir='rtl'] {
  .ef-header__top-link {
    .ef-header__icon {
      order: -1;
      margin-left: 0;
      margin-right: 0;
    }
  }

  .ef-header__icon {
    &.-mr {
      margin-left: 12px;
    }

    &.-ml {
      margin-right: 12px;
    }
  }

  .ef-header__col.-right {
    text-align: left;
  }

  .ef-header__menu-toggle {
    right: auto;
    left: -24px;
  }
}
