@import '../base/color';

.ef-link {
  position: relative;
  z-index: 1;
  display: inline-block;
  color: currentColor;

  &:before {
    content: '';
    position: absolute;
    top: 2px;
    right: -1px;
    bottom: 0;
    left: 0;
    z-index: -1;
    transform: rotateX(88deg);
    transform-origin: bottom;
    border-radius: 2px;
    background: currentColor;
    transition: 0.12s transform, background 0s 0.12s;
  }

  &:hover,
  &:focus {
    color: currentColor;
    text-decoration: none;
    &:before {
      transform: rotateX(60deg);
      background: var(--color-link-bg, $color-link-bg);
      transition: 0.12s transform;
    }
  }

  &:active {
    color: currentColor;
    text-decoration: none;
    &:before {
      transform: rotateX(0);
      background: var(--color-link-bg, $color-link-bg);
      transition: 0.12s transform;
    }
  }
}

.ef-link--arrow {
  display: inline-flex;
  align-items: center;
  color: var(--color-text, $color-text);

  &:hover {
    color: var(--color-text, $color-text);
    text-decoration: none;
  }

  &.-block {
    display: flex;
    justify-content: space-between;
  }

  &-svg {
    display: inline-flex;
    margin-left: 8px;
    transition: transform 0.18s;
  }

  &:hover,
  a.ef-card--content:hover &:hover {
    .ef-link--arrow-svg {
      transform: translateX(6px);
    }
  }
}
