//
// Text inputs
//
@import '../settings/variables/color';
@import '../settings/mixins/common';
@import '../settings/mixins/icons';
@import '../settings/mixins/spinner';

// Reset styles
.ef-input {
  // Placeholder needs opacity set for firefox.
  &::placeholder,
  option[disabled] {
    color: currentColor;
    opacity: 0.5;
  }

  // hide the caret on `<select>`s in IE10+.
  &::-ms-expand {
    display: none;
  }
}

// Firefox - hide number spinner
.ef-input[type='number'] {
  -moz-appearance: textfield;
}

// Webkit browsers - hide number spinner
.ef-input[type='number']::-webkit-inner-spin-button,
.ef-input[type='number']::-webkit-outer-spin-button {
  margin: 0;
  -webkit-appearance: none;
}

// Input wrappers
.ef-input-w {
  position: relative;
  display: flex;
  align-items: center;
  transition: all 0.15s;
}

// Text style inputs
/* stylelint-disable-next-line */
.ef-input {
  width: 100%;
  height: 48px;
  padding: 12px pad(s);
  display: block;
  font-size: 16px;
  line-height: 24px;
  color: $color-text;
  background-color: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: 4px;

  &:focus,
  &.-focus {
    @include inputFocusShadow();
    outline: none;
    border-color: var(--color-ui-accent, $color-ui-accent);
    transition: border 0.25s linear,
      box-shadow 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  &:disabled,
  &[disabled],
  &[readonly] {
    color: $color-text--disabled;
    background-color: var(--input-bg--disabled);
    border-color: var(--input-border--disabled);
    box-shadow: none;
    user-select: none;
  }
}

// Adds ellipsis to overflowing, unfocused inputs
input.ef-input {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

textarea.ef-input {
  min-height: 100px;
  min-width: 100px;
  transition: none;
}

// Loading styles
.ef-input-w {
  &.-is-loading {
    @include loadingSpinner();

    .ef-input {
      @extend .-is-disabled !optional;
    }

    &:after {
      left: auto;
      right: 12px;
    }

    .ef-button__content {
      visibility: hidden;
    }
  }
}

// Text input validation styles
.ef-input-w:not(.ef-boolean) {
  &.-is-valid,
  &.-is-invalid {
    &:after {
      content: '';
      @include iconBG(validation/check-circle-validation);
      position: relative;
      width: 16px;
      height: 16px;
      z-index: 1;
      transform: scale(0);
      display: block;
      border-radius: 50%;
      margin-left: -32px;
      transition: transform 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      pointer-events: none;
    }

    .ef-input {
      padding-right: 32px;

      &:not(:focus) {
        box-shadow: none;
      }
    }
  }

  &.-is-valid {
    &:after {
      transform: scale(1);
    }
  }

  &.-is-invalid {
    .ef-input {
      color: $color-notification-warning;
      border-color: $color-notification-warning;

      &:focus {
        box-shadow: 0 4px 12px 0 rgba($color-notification-warning, 0.2);
      }
    }

    &:after {
      @include iconBG(validation/error-circle-validation);
      transform: scale(1);
    }
  }

  &.-textarea.-is-valid,
  &.-textarea.-is-invalid {
    &:after {
      align-self: flex-start;
      top: 16px;
    }
  }
}
