// Solid
$surface--solid--box-shadow-hover: 0 2px 8px 0 rgba(25, 25, 25, 0.16);

// Outlined
$surface--outline--box-shadow-hover: $surface--solid--box-shadow-hover;

// Shadow
$surface--shadow--box-shadow: 0 2px 8px 0 rgba(25, 25, 25, 0.16);
$surface--shadow--box-shadow-hover: 0 2px 16px 0 rgba(25, 25, 25, 0.1);

// Deep Shadow
$surface--deep-shadow--box-shadow: 0 2px 16px 0 rgba(25, 25, 25, 0.16);
$surface--deep-shadow--box-shadow-hover: 0 2px 32px 0 rgba(25, 25, 25, 0.16);

$surface--hover-transition: box-shadow 0.3s;


:root {
  // Solid
  --surface--solid--box-shadow-hover: #{$surface--solid--box-shadow-hover};

  // Outlined
  --surface--outline--box-shadow-hover: var(--surface--solid--box-shadow-hover, $surface--solid--box-shadow-hover);

  // Shadow
  --surface--shadow--box-shadow: #{$surface--shadow--box-shadow};
  --surface--shadow--box-shadow-hover: #{$surface--shadow--box-shadow-hover};

  // Deep Shadow
  --surface--deep-shadow--box-shadow: #{$surface--deep-shadow--box-shadow};
  --surface--deep-shadow--box-shadow-hover: #{$surface--deep-shadow--box-shadow-hover};

  --surface--hover-transition: #{$surface--hover-transition};
}
