@import '../typography/variables/font-weights';
@import '../settings/variables/color';
@import '../settings/variables/spacing';
@import '../settings/variables/assets';
@import '../settings/mixins/breakpoints';
@import '../settings/mixins/common';
@import '../settings/utils/map';
// Helper for handling icon sprites, pass matrix positions instead of px
@mixin spriter($row, $col, $size: 32, $scale: 2) {
  $posX: (($col - 1) * $size) / $scale + px;
  $posY: (($row - 1) * $size) / $scale + px;
  background-position: -#{$posX} -#{$posY};
}

.ef-footer {
  font-weight: var(--fw-book, $fw-book);
  color: var(--color-text, $color-text);
  background-color: var(--color-education-paper, $color-education-paper);
  font-size: 14px;
  padding-top: $layout-unit * 2 + px;
  margin-top: auto;

  @include breakpoint(m) {
    font-size: 12px;
  }
}

.ef-footer__social-list {
  display: flex;
}

.ef-footer__social-link {
  width: 24px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-left: pad(s);
  line-height: 24px;
  font-size: 16px;
  text-align: center;
}

.ef-footer__title {
  position: relative;
  max-width: none;
  color: var(--color-text, $color-text);
  font-size: 14px;
  margin-bottom: pad(s);
  padding-bottom: pad(s);
  font-weight: var(--fw-bold, $fw-bold);
  @include ellipsis();

  @include breakpoint(m) {
    font-size: 12px;
    border-bottom: 1px solid var(--color-text, $color-text);
  }

  &[aria-expanded='true'] {
    border-bottom: 1px solid var(--color-text, $color-text);
  }
}

.ef-footer__list {
  margin: pad(s) 0 pad(m);
  padding: 0;
  list-style: none;

  @include breakpoint(m) {
    margin-bottom: pad(xl);
  }

  .ef-footer__lang-section &,
  .ef-footer__language-dd & {
    margin-bottom: pad(xl);
  }

  .ef-list__item {
    margin-bottom: pad(m);

    &:last-child {
      margin-bottom: 0;
    }

    @include breakpoint(m) {
      margin-bottom: 0;
    }
  }

  &.ef-collapsible__content[aria-hidden='false'] {
    padding: pad(s) 0 pad(l);
  }
}

.ef-footer__icon {
  .-inv & {
    filter: grayscale(1) brightness(10);
  }
}

.ef-footer {
  .ef-logo {
    width: 89px;
    height: 60px;

    svg {
      width: 100%;
      height: 100%;
      position: relative;
    }
  }

  @include breakpoint(s only) {
    .ef-input-w.-select {
      margin: pad(s) 0 pad(m);
    }
  }
}

.ef-footer__link {
  font-size: 14px;
  color: var(--color-text, $color-text);

  @include breakpoint(m) {
    display: inline-block;
    font-size: 12px;
  }

  &[data-dd-toggle]::after {
    background-size: 8px;
  }

  &[target='_blank'] {
    position: relative;

    svg {
      width: 8px;
      height: 8px;
      margin-left: 2px;
    }
  }
}

.ef-footer__lang-selector-w {
  padding: pad(s) 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid var(--color-ui-border);

  @include breakpoint(s only) {
    padding: pad(l) 0 pad(s);
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: pad(s);
  }
}

.ef-footer__lang-selector,
.ef-footer__lang-selector .ef-collapsible__toggle {
  @include breakpoint(s only) {
    width: 100%;
  }
}

.ef-footer__colophon {
  @include breakpoint(s only) {
    padding-bottom: pad(l);
  }
  @include breakpoint(m) {
    .ef-list {
      margin: 0;
      display: flex;
    }

    .ef-list__item {
      margin-bottom: 0;

      &:not(:first-child) {
        margin-left: pad(m);
      }

      &:last-child {
        margin-left: 70px;
      }
    }
  }
}

.ef-footer__link[class*='-flag-'] {
  display: inline-flex;
  align-items: center;

  &:before {
    content: '';
    width: 16px;
    height: 16px;
    display: block;
    margin-right: pad(xs);
    background-image: url('#{$imagesPath}flag-sprite.png');
    background-size: 128px;
  }
}

