// generate flag sprite classes and positioning
@each $country, $coords in $flags {
  $row: map-deep-get($flags, $country, 'row');
  $col: map-deep-get($flags, $country, 'col');
  $country: to_lower_case($country);

  .-flag-#{$country}:before {
    @include spriter($row, $col);
  }
}
