@import '../settings/variables/color';

// Condensed
.ef-pagination--sm {
  display: flex;
  align-items: center;
}

.ef-pagination--sm__btn {
  padding-left: 16px;
  padding-right: 16px;

  &:first-child,
  &:last-child {
    padding-left: 8px;
    padding-right: 8px;

    .ef-icon {
      transform: scale(0.8);
    }
  }
}

.ef-pagination--sm__content {
  flex: 1 1 auto;
  font-weight: normal;
  text-align: center;
}

// Expanded
.ef-pagination--lg {
  display: flex;
  align-items: center;
}

.ef-pagination--lg__content {
  display: flex;
  flex: 1 1 auto;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: normal;
}

.ef-pagination--lg__page {
  width: 40px;
  margin: 0 15px;
  display: inline-block;
  line-height: 40px;
  border-radius: 4px;

  &.-is-active {
    background: var(--color-education-paper, $color-education-paper);
  }

  &.-more {
    margin: 0 30px;
  }
}
