@import '../settings/mixins/grid';
@import '../settings/variables/breakpoints';

.ef-container {
  @include container();
}

.ef-row {
  display: flex;
  flex: 0 1 auto;
  flex-wrap: wrap;
  @include row();
}

.ef-col {
  @include col-common;
}

@each $size, $gutter in $col-gutters {
  @include breakpoint(#{$size} only) {
    $half-gutter: $gutter * 0.5;
    .ef-row {
      margin-left: -$half-gutter + px;
      margin-right: -$half-gutter + px;
    }
    .ef-col {
      padding-right: $half-gutter + px;
      padding-left: $half-gutter + px;
    }
  }
}

@each $breakpoint, $size in $breakpoints {
  $i: index($breakpoints, $breakpoint);

  $prefix: '-' + $breakpoint;

  .ef-col {
    @if ($size > 0) {
      @include breakpoint($breakpoint) {
        @include col($prefix);
      }
      @include breakpoint($breakpoint) {
        &.#{$prefix}-auto {
          max-width: 100%;
          flex-basis: 0;
        }
      }
    } @else {
      @include col(
        $prefix
      ); // for initial breakpoint - in our scenario "s" for small
    }
  }
}

// Stack component
.ef-stack {
  height: 100%;
  display: flex;
  flex-direction: column;

  &.-between {
    justify-content: space-between;
  }

  &.-around {
    justify-content: space-around;
  }

  &.-start {
    justify-content: flex-start;
  }

  &.-end {
    justify-content: flex-end;
  }
}

.ef-stack__item {
  flex: 0 0 auto;

  &.-stretch {
    flex-grow: 1;
  }

  &.-start {
    margin-bottom: auto;
  }

  &.-end {
    margin-top: auto;
  }
}
